import { t } from 'i18next';
import { subDays } from 'date-fns';
import ImageEditor from '@/screens/Chamber/components/elements/ImageElement/ImageEditor';
import { TextArea } from '@/components/TextArea/TextArea';
import { DatePicker } from '@/components/DatePicker';
import { type EditElementProps } from './types';
import { StyledTabWrapper } from './styles';

export const EditElement = ({
  question,
  date,
  url,
  handleChangeTitle,
  handleChangeDate,
  onContentChange,
}: EditElementProps) => (
  <StyledTabWrapper data-testid="EditElementWrapper" fullWidth>
    <TextArea
      data-testid="tTextArea"
      placeholder={t('noumena.chamber.quick_question.question')}
      value={question}
      resize={false}
      maxLength={250}
      onChange={(e: React.ChangeEvent<HTMLTextAreaElement>) =>
        handleChangeTitle(e.target.value)
      }
    />
    <DatePicker
      minDate={new Date()}
      required
      placement="bottom-end"
      value={date && new Date(date)}
      label={t('noumena.chamber.quick_question.expiry_date')}
      onChange={handleChangeDate}
      disabled={{ from: new Date(0), to: subDays(new Date(), 1) }}
    />

    <ImageEditor
      url={url}
      onContentChange={onContentChange}
      noumEditor2={false}
    />
  </StyledTabWrapper>
);
