import { t } from 'i18next';

import { EventsFilter } from '@/apollo/generated/types';
import { getSingularPluralKey } from '@/utils/strings';

import { type CalendarOwnerEventsListCollapsedProps } from './types';
import {
  OwnerEventsListCollapsedWrapper,
  OwnerEventsListCollapsedItem,
  EventCategory,
  EventCount,
  EventHelper,
} from './styles';

export const CalendarOwnerEventsListCollapsed = ({
  activeFilter,
  onFilterChange,
  eventMeta,
}: CalendarOwnerEventsListCollapsedProps) => (
  <OwnerEventsListCollapsedWrapper data-testid="owner-events-list-collapsed">
    <OwnerEventsListCollapsedItem
      active={activeFilter === EventsFilter.Attending}
      onClick={() => onFilterChange(EventsFilter.Attending)}
    >
      <EventCategory
        font="body-m-bold"
        colorToken={
          activeFilter === EventsFilter.Attending
            ? '--text-summary-button-brand-primary-highlighted'
            : '--text-summary-button-neutral-highlighted'
        }
      >
        {t('noumena.homeChambers.event.filter_attending')}
      </EventCategory>
      <EventCount
        font="body-xl-bold"
        colorToken={
          activeFilter === EventsFilter.Attending
            ? '--text-summary-button-brand-primary-highlighted'
            : '--text-summary-button-neutral-highlighted'
        }
      >
        {eventMeta?.acceptedEventsCount || 0}
      </EventCount>
      <EventHelper
        font="footnote-bold"
        colorToken={
          activeFilter === EventsFilter.Attending
            ? '--text-summary-button-brand-primary-default'
            : '--text-summary-button-neutral-default'
        }
      >
        {t(
          getSingularPluralKey(
            'noumena.homeChambers.event.event',
            eventMeta?.acceptedEventsCount || 0,
          ),
        )}
      </EventHelper>
    </OwnerEventsListCollapsedItem>

    <OwnerEventsListCollapsedItem
      active={activeFilter === EventsFilter.Hosting}
      onClick={() => onFilterChange(EventsFilter.Hosting)}
    >
      <EventCategory
        font="body-m-bold"
        colorToken={
          activeFilter === EventsFilter.Hosting
            ? '--text-summary-button-brand-primary-highlighted'
            : '--text-summary-button-neutral-highlighted'
        }
      >
        {t('noumena.homeChambers.event.filter_hosting')}
      </EventCategory>
      <EventCount
        font="body-xl-bold"
        colorToken={
          activeFilter === EventsFilter.Hosting
            ? '--text-summary-button-brand-primary-highlighted'
            : '--text-summary-button-neutral-highlighted'
        }
      >
        {eventMeta?.hostedEventsCount || 0}
      </EventCount>
      <EventHelper
        font="footnote-bold"
        colorToken={
          activeFilter === EventsFilter.Hosting
            ? '--text-summary-button-brand-primary-default'
            : '--text-summary-button-neutral-default'
        }
      >
        {t(
          getSingularPluralKey(
            'noumena.homeChambers.event.event',
            eventMeta?.hostedEventsCount || 0,
          ),
        )}
      </EventHelper>
    </OwnerEventsListCollapsedItem>

    <OwnerEventsListCollapsedItem
      active={activeFilter === EventsFilter.Invitation}
      onClick={() => onFilterChange(EventsFilter.Invitation)}
    >
      <EventCategory
        font="body-m-bold"
        colorToken={
          activeFilter === EventsFilter.Invitation
            ? '--text-summary-button-brand-primary-highlighted'
            : '--text-summary-button-neutral-highlighted'
        }
      >
        {t('noumena.homeChambers.event.filter_invites')}
      </EventCategory>
      <EventCount
        font="body-xl-bold"
        colorToken={
          activeFilter === EventsFilter.Invitation
            ? '--text-summary-button-brand-primary-highlighted'
            : '--text-summary-button-neutral-highlighted'
        }
      >
        {eventMeta?.pendingEventsCount || 0}
      </EventCount>
      <EventHelper
        font="footnote-bold"
        textAlign="center"
        colorToken={
          activeFilter === EventsFilter.Invitation
            ? '--text-summary-button-brand-primary-default'
            : '--text-summary-button-neutral-default'
        }
      >
        {t('noumena.homeChambers.event.event_without_reply')}
      </EventHelper>
    </OwnerEventsListCollapsedItem>
  </OwnerEventsListCollapsedWrapper>
);
