import { Button } from '@/components/Button';
import { CustomPreviewTabEnum } from '@/screens/Chamber/CustomPreview/constants';
import { Dropdown, type DropdownTargetProps } from '@/components/Dropdown';
import { useLaunchDarkly } from '@/hooks';
import { addEventDropdownOptions } from '@/screens/Chamber/components/elements/CalendarEvent/CalendarElement/constants';
import { Icon } from '@/components/Icon';
import { type AddEventButtonProps } from './types';

const AddEventButton = ({
  isOwner,
  handleDropdownClick,
  isAnotherEventLive,
  selectedCustomPreviewTab,
  onAddEvent,
  isNoumEditor,
}: AddEventButtonProps) => {
  const { flags } = useLaunchDarkly();
  return (
    <>
      {flags.shInstantEvent ? (
        isOwner &&
        selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview && (
          <Dropdown
            hideIcons
            onSelectOption={handleDropdownClick}
            options={addEventDropdownOptions(isAnotherEventLive)}
            usePortal={false}
            observerMinHeight="0"
            containerPadding="12px"
            containerWidth={isAnotherEventLive ? 'auto' : '150px'}
            padding="0"
          >
            {({
              targetProps,
              targetRef,
            }: DropdownTargetProps<HTMLButtonElement>) => (
              <Button
                ref={targetRef}
                {...targetProps}
                size="small"
                icon={
                  <Icon
                    name="add_s"
                    size={16}
                    color={
                      isNoumEditor
                        ? '--icon-button-brand-secondary-default'
                        : '--icon-button-neutral-alt-default'
                    }
                  />
                }
                primary={!isNoumEditor}
                secondary={isNoumEditor}
              />
            )}
          </Dropdown>
        )
      ) : (
        <Button
          onClick={onAddEvent}
          size="small"
          icon={
            <Icon
              name="add_s"
              size={16}
              color={
                isNoumEditor
                  ? '--icon-button-brand-secondary-default'
                  : '--icon-button-neutral-alt-default'
              }
            />
          }
          primary={!isNoumEditor}
          secondary={isNoumEditor}
        />
      )}
    </>
  );
};

export default AddEventButton;
