import { useTranslation } from 'react-i18next';
import {
  ElementWrapper,
  type ElementWrapperProps,
} from '@/screens/Chamber/components/ElementWrapper';
import { TSpan } from '@/components/Typography';
import { Stack } from '@/layout';
import { useSkeletonIsLoadingContext } from '@/components/SkeletonLoader/SkeletonLoaderProvider';
import SkeletonLoaderPostElement from '@/screens/Chamber/components/elements/SkeletonLoader/SkeletonLoaderPostElement';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { useLaunchDarkly } from '@/hooks';
import { useMemo } from 'react';
import { useEditChamberState } from '@/screens/Chamber/EditChamber/provider';
import { PostElementProvider } from './PostElementProvider';
import PostViewMode from './components/PostViewMode';

const PostElement = (props: ElementWrapperProps) => {
  const { t } = useTranslation();
  const { isLoading } = useSkeletonIsLoadingContext();
  const { space } = useNoumContext();
  const { space: editSpace } = useEditChamberState();

  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();

  const spaceView = useMemo(
    () => (props.isEditing ? editSpace : space),
    [editSpace, props.isEditing, space],
  );

  if (isLoading) return <SkeletonLoaderPostElement />;

  if (props.isEditing && (!noumEditor2 || props.isCustomPreview))
    return (
      <ElementWrapper {...props}>
        {!props.isCustomPreview ? (
          <Stack justify="center">
            <TSpan font="body-m" colorToken="--text-card-neutral-default">
              {t('noumena.post.edit_mode_message')}
            </TSpan>
          </Stack>
        ) : (
          <></>
        )}
      </ElementWrapper>
    );
  return (
    <PostElementProvider
      space={spaceView!}
      isCustomPreview={props.isCustomPreview}
    >
      <PostViewMode {...props} />
    </PostElementProvider>
  );
};
export default PostElement;
