import { forwardRef, type Ref, useState, useMemo, useEffect } from 'react';
import { t } from 'i18next';
import { TSpan } from '@/components/Typography';
import { Icon } from '@/components/Icon';
import { useQuickQuestionsElementContext } from '@/screens/Chamber/components/elements/QuickQuestionsElement/providers/QuickQuestionsElementProvider';
import QuickQuestionsElementHeader from '@/screens/Chamber/components/elements/QuickQuestionsElement/components/QuickQuestionsElementHeader';
import { Button } from '@/components/Button/Button';
import AddQuestion from '@/screens/Chamber/components/elements/QuickQuestionsElement/modals/AddQuestion/AddQuestion';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { Spinner } from '@/components/Spinner';
import { QuestionAnswer } from '@/screens/Chamber/components/elements/QuickQuestionsElement/components/QuestionAnswer';
import BasicChipsTabsForm from '@/components/Tabs/TabsForm';
import { QuestionStatusEnum } from '@/apollo/generated/types';
import { Pagination } from '@/components/Pagination/Pagination';
import { Spacer } from '@/layout';
import { useAuth } from '@/features/auth/contexts';
import { useLaunchDarkly } from '@/hooks';
import { cleanList } from '@/utils/list';
import { useGetQuickQuestionHelper } from './hooks/useGetQuickQuestionHelper';
import { PAGE_SIZE, questionStatus } from './constants';
import {
  AddFirstQuestionButtonTestId,
  NoQuestionsAddedYetTextTestId,
  NoQuestionsIconTestId,
  QuickQuestionsWrapperTestId,
} from './testIds';
import {
  HeaderWrapper,
  MainWrapper,
  QuestionWrapper,
  QuickQuestionsBodyWrapper,
  SpinnerWrapper,
  TabWrapper,
  TextPadding,
  Wrapper,
} from './styles';
import { type QuickQuestionsElementProps } from './types';
import { questionsMockData } from './data';

const NoQuestions: React.FC<{
  isCollapse: boolean;
  isAllowed: boolean;
  openAddQuestionModal: () => void;
  totalQuestionCount: number;
  openQuestionCount: number;
  closeQuestionCount: number;
  selectedTab: QuestionStatusEnum | undefined;
}> = ({
  isCollapse,
  isAllowed,
  openAddQuestionModal,
  totalQuestionCount,
  openQuestionCount,
  closeQuestionCount,
  selectedTab,
}) => {
  let text = t(`noumena.quick_questions.no_questions_added_yet`);
  if (selectedTab === QuestionStatusEnum.Active && closeQuestionCount) {
    text = t(`noumena.quick_questions.no_active_questions`, {
      count: closeQuestionCount,
    });
  }
  if (selectedTab === QuestionStatusEnum.Closed && openQuestionCount) {
    text = t(`noumena.quick_questions.no_closed_questions`);
  }
  const askButtonEnabled = !isCollapse && isAllowed && openQuestionCount === 0;
  return (
    <QuickQuestionsBodyWrapper isCollapse={isCollapse}>
      {!isCollapse && (
        <Icon
          data-testid={NoQuestionsIconTestId}
          name="quick_questions_m"
          size={80}
          color="--icon-placeholder-neutral-default"
        />
      )}
      <TextPadding isCollapse={isCollapse}>
        <TSpan
          data-testid={NoQuestionsAddedYetTextTestId}
          colorToken="--text-placeholder-neutral-default"
          font="body-m"
        >
          {text}
        </TSpan>
      </TextPadding>
      {askButtonEnabled && (
        <Button
          data-testid={AddFirstQuestionButtonTestId}
          size="small"
          secondary
          onClick={openAddQuestionModal}
        >
          {totalQuestionCount
            ? t('noumena.chamber.quick_question.add_qustion')
            : t('noumena.chamber.quick_question.add_first_qustion')}
        </Button>
      )}
    </QuickQuestionsBodyWrapper>
  );
};

const QuickQuestionsElementViewMode = forwardRef(
  (
    {
      isEditing,
      spaceId,
      currentTitle,
      selectedCustomPreviewTab,
      isOwner,
    }: QuickQuestionsElementProps,
    ref: Ref<HTMLDivElement>,
  ) => {
    const [isAddModalOpen, setIsAddModalOpen] = useState(false);
    const [offset, setOffset] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const [selectedTab, setSelectedTab] = useState<
      QuestionStatusEnum | undefined
    >(undefined);

    const {
      flags: { webTips, noumEditor2 },
    } = useLaunchDarkly();
    const { isUnregistered } = useAuth();
    const { isCollapse } = useQuickQuestionsElementContext();
    const { isConnected, isFollowing } = useNoumContext();
    const {
      data,
      loading,
      refetch,
      totalQuestionCount,
      openQuestionCount,
      closeQuestionCount,
      answeredQuestionCount,
      tippedQuestionCount,
    } = useGetQuickQuestionHelper(spaceId, selectedTab, isCollapse, offset);
    const isShowPlaceholder = useMemo(
      () => noumEditor2 && isEditing && totalQuestionCount < 1,
      [isEditing, noumEditor2, totalQuestionCount],
    );

    const availableTabOptions = useMemo(() => {
      const availableTabs: string[] = [
        QuestionStatusEnum.Active,
        QuestionStatusEnum.Closed,
      ];
      if (isShowPlaceholder) {
        availableTabs.push(
          QuestionStatusEnum.Answered,
          QuestionStatusEnum.Tipped,
        );
      } else {
        if (answeredQuestionCount) {
          availableTabs.push(QuestionStatusEnum.Answered);
        }
        if (webTips && tippedQuestionCount) {
          availableTabs.push(QuestionStatusEnum.Tipped);
        }
      }
      return [...questionStatus].filter((item) =>
        availableTabs.includes(item.name),
      );
    }, [
      answeredQuestionCount,
      isShowPlaceholder,
      tippedQuestionCount,
      webTips,
    ]);

    const onTabChange = (value: string) => {
      setSelectedTab(value as QuestionStatusEnum);
      setCurrentPage(1);
      setOffset(0);
    };

    const closeAddModal = () => {
      setIsAddModalOpen(false);
    };

    const openAddQuestionModal = () => {
      setIsAddModalOpen(true);
    };

    const isAllowed = useMemo(() => {
      if (isUnregistered) return false;
      return isOwner || isConnected || (noumEditor2 && isEditing);
    }, [isConnected, isEditing, isOwner, isUnregistered, noumEditor2]);
    const dataQuestions = useMemo(
      () =>
        cleanList(
          isShowPlaceholder
            ? questionsMockData.getQuestionsForSpace?.data
            : data?.getQuestionsForSpace?.data,
        ),
      [data?.getQuestionsForSpace?.data, isShowPlaceholder],
    );
    const onPageChange = (page: number) => {
      setCurrentPage(page);
      setOffset((page - 1) * PAGE_SIZE);
    };

    useEffect(() => {
      if (!isCollapse) {
        onTabChange(QuestionStatusEnum.Active);
      } else {
        setSelectedTab(undefined);
      }
    }, [isCollapse]);

    useEffect(() => {
      if (closeQuestionCount < 1 && selectedTab === QuestionStatusEnum.Closed) {
        onTabChange(QuestionStatusEnum.Active);
      }
      if (openQuestionCount < 1 && selectedTab === QuestionStatusEnum.Active) {
        onTabChange(QuestionStatusEnum.Closed);
      }
      // cause loop if we add selectedTac in dependencies
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [closeQuestionCount, openQuestionCount]);

    return (
      <>
        {((isFollowing && dataQuestions && dataQuestions.length > 0) ||
          totalQuestionCount ||
          isAllowed) && (
          <Wrapper ref={ref} data-testid={QuickQuestionsWrapperTestId}>
            <MainWrapper>
              <HeaderWrapper isElement noumEditor2={noumEditor2}>
                <QuickQuestionsElementHeader
                  spaceId={spaceId}
                  currentTitle={currentTitle}
                  isAllowed={isAllowed}
                  openAddQuestionModal={openAddQuestionModal}
                  selectedCustomPreviewTab={selectedCustomPreviewTab}
                  openQuestionCount={openQuestionCount}
                />
              </HeaderWrapper>
              {!isCollapse && selectedTab && (
                <TabWrapper noumEditor2={noumEditor2}>
                  <BasicChipsTabsForm
                    onChange={onTabChange}
                    inputList={availableTabOptions}
                    selectedId={selectedTab}
                    mode={noumEditor2 ? 'isUnderline' : 'isBackground'}
                    isWithoutImage
                    fontSize="--font-link-medium-size"
                    textFont="--font-body-medium-regular-font
                    "
                  />
                </TabWrapper>
              )}
              {loading ? (
                <SpinnerWrapper>
                  <Spinner />
                </SpinnerWrapper>
              ) : dataQuestions && dataQuestions.length > 0 ? (
                <QuestionWrapper noumEditor2={noumEditor2}>
                  {dataQuestions.map((question) => (
                    <QuestionAnswer
                      key={question?._id}
                      question={question}
                      isCollapse={isCollapse}
                      selectedCustomPreviewTab={selectedCustomPreviewTab}
                      selectedTab={selectedTab}
                      refetch={refetch}
                      isOwner={isOwner}
                    />
                  ))}
                  {!!data?.getQuestionsForSpace?.count &&
                    data?.getQuestionsForSpace?.count > PAGE_SIZE &&
                    !isCollapse && (
                      <div>
                        <Spacer height={4} />
                        <Pagination
                          currentPage={currentPage}
                          pageSize={PAGE_SIZE}
                          totalCount={data.getQuestionsForSpace.count}
                          onPageChange={onPageChange}
                        />
                      </div>
                    )}
                </QuestionWrapper>
              ) : (
                <NoQuestions
                  totalQuestionCount={totalQuestionCount}
                  isAllowed={!!isAllowed}
                  isCollapse={isCollapse}
                  openAddQuestionModal={openAddQuestionModal}
                  openQuestionCount={openQuestionCount}
                  closeQuestionCount={closeQuestionCount}
                  selectedTab={selectedTab}
                />
              )}
              <AddQuestion
                spaceId={spaceId}
                onClose={closeAddModal}
                isOpen={isAddModalOpen}
                refetch={refetch}
              />
            </MainWrapper>
          </Wrapper>
        )}
      </>
    );
  },
);

export default QuickQuestionsElementViewMode;
