import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  ModalSize,
} from '@/components/ExtendedModal';
import { TSpan } from '@/components/Typography';
import { Button } from '@/components/Button/Button';
import { useEvents, useEventTimeSlot } from '@/features/events/hooks';
import { Spacer } from '@/layout';
import Timer from '@/components/Timer/Timer';
import { getLocalTime } from '@/utils/date';
import { type EventFragment } from '@/apollo/graphql';
import { EventItem } from '@/features/events/components';
import {
  withRecurringEvent,
  type WithRecurringEventProps,
} from '../../elements/CalendarEvent/modals/EventListModal/withRecurringEvent';

const pageSize = 3;

export const StartNowEventWarningModal: React.FC<
  {
    isOpen: boolean;
    onClose: () => void;
    onContinueClick: () => void;
    event: EventFragment;
    spaceId: string;
  } & Partial<WithRecurringEventProps>
> = withRecurringEvent(
  ({
    isOpen,
    onClose,
    event,
    spaceId,
    onContinueClick,
    onEventAccepted,
    onEventDeclined,
  }) => {
    const {
      onGoLive,
      onAccept,
      onDecline,
      onAttending,
      onNotAttending,
      onAttend,
    } = useEvents({
      chamberId: spaceId,
      limit: pageSize,
      preventGetEvents: true,
    });

    const { t } = useTranslation();

    const localDateTime = getLocalTime({
      dateTime: new Date(event?.eventDate),
    });

    const { diffSeconds } = useEventTimeSlot({
      startTimestamp: localDateTime?.getTime() || 0,
      durationInSeconds: event?.duration || 0,
      status: event?.status,
    });

    return (
      <Modal
        onClose={onClose}
        enableCloseButton
        open={isOpen}
        size={ModalSize.M}
        disableBackdropClick
      >
        <ModalHeader>{t('noumena.event.modal.start_now')}</ModalHeader>
        <ModalBody>
          <TSpan
            font="body-m"
            textAlign="center"
            colorToken="--text-modal-neutral-default"
          >
            {diffSeconds < 0 ? (
              t('noumena.event.modal.another_event_scheduled_now')
            ) : (
              <>
                {t('noumena.event.modal.another_event_scheduled_in')}
                <Timer initialSeconds={diffSeconds} unit="minute" />:
              </>
            )}
          </TSpan>

          <Spacer height={16} />
          {event && spaceId && (
            <EventItem
              key={event._id}
              event={event}
              chamberId={spaceId || ''}
              type="calendar"
              onGoLive={() => onGoLive(event._id || '')}
              onAccept={() => onEventAccepted?.(event, onAccept)!}
              onDecline={() => onEventDeclined?.(event, onDecline)!}
              onAttending={() => onEventAccepted?.(event, onAttending)!}
              onNotAttending={() => onEventDeclined?.(event, onNotAttending)!}
              onEditEvent={() => {}}
              onJoinEvent={() => {}}
              onAttend={() => onEventAccepted?.(event, onAttend)!}
              onClickSeeMore={() => {}}
              onViewAttendees={() => {}}
              hideActionButtons
              notClickable
              hideDescription
            />
          )}
          <Spacer height={16} />
          <TSpan
            font="body-m"
            textAlign="center"
            colorToken="--text-modal-neutral-default"
          >
            {t('noumena.event.modal.start_now.reminder')}
          </TSpan>
        </ModalBody>
        <ModalFooter gap={16} flexDirection="row" marginTop={12}>
          <Button
            tertiary
            intent="negative"
            size="full"
            onClick={onClose}
            data-testid="cancel-button"
          >
            {t('noumena.event.modal.cancel')}
          </Button>
          <Button
            onClick={onContinueClick}
            primary
            size="full"
            data-testid="confirm-button"
          >
            {t('noumena.event.modal.continue')}
          </Button>
        </ModalFooter>
      </Modal>
    );
  },
);
