import { useMemo, useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { Avatar } from '@/components/Avatar/Avatar';
import { DiagonalAvatar3 } from '@/components/Avatar/Diagonal3/Diagonal3';
import { TSpan } from '@/components/Typography';
import { getFullName } from '@/utils/fullName';
import { UserUtil } from '@/utils/user';
import { usePostElement } from '../../PostElementProvider';
import { Container, AllTab, UserFrame, UserItem, MoreText } from './styles';

const TO_SHOW = 6;

const AuthorsList = () => {
  const { authors, filter, setFilter, noumEditor2, isCustomPreview } =
    usePostElement();
  const { t } = useTranslation();

  const selectedUid = useMemo(
    () => filter.filter?.uid || 'all',
    [filter.filter],
  );

  const all = useMemo(() => [...authors], [authors]);

  const remainCount = useMemo(() => all.length - TO_SHOW, [all]);

  const lastUrls = useMemo(() => {
    if (remainCount > 0) {
      const tmp = all.slice(TO_SHOW, TO_SHOW + 3);
      const urls: string[] = [];
      tmp.map((item) => {
        urls.push(item.profile?.profilePictureThumbnail || '');
        return undefined;
      });
      return urls;
    }
    return [];
  }, [all, remainCount]);

  const handleSelect = useCallback(
    (uid) => {
      if (uid === 'all') {
        setFilter({
          ...filter,
          filter: {
            ...filter.filter,
            uid: undefined,
          },
        });
      } else {
        setFilter({
          ...filter,
          filter: {
            ...filter.filter,
            uid,
          },
        });
      }
    },
    [filter, setFilter],
  );

  return (
    <Container noumEditor2={noumEditor2 && !isCustomPreview}>
      <AllTab
        onClick={() => {
          handleSelect('all');
        }}
        size="large"
        borderRadius="8px"
        secondary={selectedUid !== 'all'}
        isDisabled={selectedUid !== 'all'}
        noumEditor2={noumEditor2 && !isCustomPreview}
      >
        {t('noumena.post_all_authors')}
      </AllTab>
      <UserFrame>
        {all.slice(0, TO_SHOW).map((author) => (
          <UserItem
            key={author._id}
            onClick={() => {
              handleSelect(author._id);
            }}
            isActive={selectedUid === author._id}
            noumEditor2={noumEditor2 && !isCustomPreview}
          >
            <Avatar size="M" url={UserUtil.getProfilePicture(author) ?? ''} />
            {selectedUid === author._id && (
              <TSpan colorToken="--text-tab-chips-brand-primary-selected">
                {getFullName(
                  author.firstName,
                  author.middleName,
                  author.lastName,
                )}
              </TSpan>
            )}
          </UserItem>
        ))}
        {remainCount > 0 && (
          <>
            <DiagonalAvatar3 size="M" urls={lastUrls} />
            <MoreText colorToken="--text-tab-chips-neutral-default">
              +{remainCount}
            </MoreText>
          </>
        )}
      </UserFrame>
    </Container>
  );
};

export default AuthorsList;
