import { type Maybe } from '@/apollo/generated/types';
import { type SpaceOutputFragment } from '@/apollo/graphql';
import { DeviceTypeEnum, useDeviceType, useLaunchDarkly } from '@/hooks';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
  type FC,
  type ReactNode,
} from 'react';

export enum WalletViewMode {
  DEFAULT = 'default',
  FULLDATA = 'full-data',
}

interface IWalletElementContext {
  loading: boolean;
  space: SpaceOutputFragment | undefined;
  spaceId: Maybe<string> | undefined;
  isCollapse: boolean;
  setIsCollapse: (value: boolean) => void;
  viewMode: WalletViewMode;
  setViewMode: (value: WalletViewMode) => void;
}

const initialValue: IWalletElementContext = {
  loading: false,
  space: undefined,
  spaceId: undefined,

  isCollapse: false,
  setIsCollapse: () => {},
  viewMode: WalletViewMode.DEFAULT,
  setViewMode: () => {},
};

export const WalletElementContext =
  createContext<IWalletElementContext>(initialValue);

export const WalletElementProvider: FC<{ children: ReactNode }> = ({
  children,
}) => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const deviceType = useDeviceType();
  const { loading, spaceId, space } = useNoumContext();
  const [isCollapse, setIsCollapse] = useState(true);
  const [viewMode, changeViewMode] = useState<WalletViewMode>(
    WalletViewMode.DEFAULT,
  );
  const setViewMode = useCallback(
    (value: WalletViewMode) => {
      if (deviceType === DeviceTypeEnum.MOBILE) {
        changeViewMode(value);
      } else {
        changeViewMode(WalletViewMode.DEFAULT);
      }
    },
    [deviceType],
  );

  useEffect(() => {
    if (deviceType === DeviceTypeEnum.MOBILE)
      changeViewMode(WalletViewMode.FULLDATA);
  }, [deviceType]);

  useEffect(() => {
    if (deviceType !== DeviceTypeEnum.MOBILE)
      changeViewMode(WalletViewMode.DEFAULT);
  }, [deviceType]);

  const value = useMemo(
    () => ({
      loading,
      spaceId,
      space,
      isCollapse: isCollapse && !noumEditor2,
      setIsCollapse,
      viewMode,
      setViewMode,
    }),
    [loading, spaceId, space, isCollapse, noumEditor2, viewMode, setViewMode],
  );

  return (
    <WalletElementContext.Provider value={value}>
      {children}
    </WalletElementContext.Provider>
  );
};

export const useWalletElement = () => {
  const value = useContext(WalletElementContext);

  return value;
};
