import { useCallback, useEffect, useState } from 'react';
import { t } from 'i18next';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  ModalSize,
} from '@/components/ExtendedModal';
import { Button } from '@/components/Button';
import { useWindowDimensions } from '@/hooks/dimensions';
import { Spacer } from '@/layout';
import { breakpoints } from '@/constants/devices';
import { type AddQuestionProps, type EditElementSchema } from './types';
import { EditElement } from './EditElement';
import { useAddQuestionHelper } from './useAddQuestionHelper';

const AddQuestion = ({
  isEmpty = true,
  spaceId,
  refetch,
  onClose,
  isOpen,
}: AddQuestionProps) => {
  const [question, setQuestion] = useState<string>('');
  const [url, setUrl] = useState<string>('');
  const [date, setDate] = useState<Date | undefined>();

  const setDateMethod = (value?: Date) => {
    setDate(value);
  };

  const onContentChange = (value: string) => {
    setUrl(value);
  };

  const elementSchema = yup
    .object({
      question: yup.string().required().max(250),
      date: yup.date().required(),
    })
    .required();

  const {
    reset,
    trigger,
    setValue,
    formState: { isValid },
  } = useForm<EditElementSchema>({
    resolver: yupResolver(elementSchema),
    mode: 'all',
    reValidateMode: 'onBlur',
  });

  const resetData = useCallback(() => {
    reset();
    setQuestion('');
    setDate(undefined);
    setUrl('');
  }, [reset]);

  const closeModal = useCallback(() => {
    resetData();
    onClose();
  }, [onClose, resetData]);

  const { width } = useWindowDimensions();
  const { handleSaveChanges, loading } = useAddQuestionHelper({
    spaceId,
    resetData,
    closeModal,
    question,
    url,
    date,
    refetch,
  });
  const isDesktop = width > breakpoints.LAPTOP - 1;

  useEffect(() => {
    setValue('question', question);
    setValue('date', date ?? new Date());
    trigger('date');
    trigger('question');
  }, [question, date, setValue, trigger]);

  return (
    <Modal
      open={isOpen}
      onClose={closeModal}
      testId="add_question_modal"
      enableCloseButton
      size={ModalSize.XL}
      disableBackdropClick
    >
      <ModalHeader>{t('noumena.chamber_edit.add_question')}</ModalHeader>
      <ModalBody tabletFlex isFullScreen={!isDesktop} minHeight="60vh">
        <EditElement
          question={question}
          date={date}
          url={url}
          handleChangeTitle={setQuestion}
          handleChangeDate={setDateMethod}
          onContentChange={onContentChange}
        />
        <Spacer height={8} />
      </ModalBody>
      <ModalFooter gap={16} marginTop={16}>
        <Button
          data-testid="cancel_button"
          tertiary
          size="full"
          onClick={closeModal}
        >
          {t('noumena.cancel')}
        </Button>
        <Button
          data-testid="ask_question_button"
          size="full"
          primary={!isEmpty || isValid}
          disabled={!isValid || loading}
          onClick={handleSaveChanges}
          loading={loading}
        >
          {t('noumena.chamber_edit.visibility.ask_question')}
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default AddQuestion;
