import { useLaunchDarkly } from '@/hooks';
import { NetworkStatus } from '@apollo/client';
import SkeletonLoaderFileManagerBody from '../../../SkeletonLoader/SkeletonLoaderFileManagerBody';
import { useFileManagerElementContext } from '../../providers/FileManagerElementProvider';
import FileListItem from '../FileListItem';
import EmptyState from './EmptyState';
import S from './styles';

type FileListProps = {
  maxFilesToDisplay?: number;
};

const FileList: React.FC<FileListProps> = ({ maxFilesToDisplay }) => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const { isCollapse, files, networkStatus, hasFiles, searchQuery } =
    useFileManagerElementContext();

  return (
    <S.ListWrapper>
      {!hasFiles ? (
        networkStatus === NetworkStatus.ready ? (
          <EmptyState isSeachResult={!!searchQuery} />
        ) : (
          <SkeletonLoaderFileManagerBody />
        )
      ) : (
        <S.Container fullWidth={files.length === 1 || noumEditor2}>
          {files
            .slice(0, maxFilesToDisplay || files.length)
            .map((file, index) =>
              // render only 2 items when file manager element is collapsed
              isCollapse && index >= 2 ? null : (
                <FileListItem key={file._id} file={file} />
              ),
            )}
        </S.Container>
      )}
    </S.ListWrapper>
  );
};

export default FileList;
