import { useCallback, useMemo } from 'react';
import { type SocialLink } from '@/apollo/generated/types';
import { TSpan } from '@/components/Typography';
import { Icon } from '@/components/Icon';
import { type Maybe } from '@/common/types';
import { getSocialHandleFromLink, getSocialNameFromLink } from '@/utils/url';
import { Container, TextWrap } from './styles';

type NetworkItemProps = {
  data: SocialLink;
  isCollapse: boolean;
};
export const NetworkItem = (props: NetworkItemProps) => {
  const { isCollapse, data } = props;

  const handle = useMemo(
    () => getSocialHandleFromLink(data?.link!),
    [data?.link],
  );

  const makeViableLink = useCallback((link: string) => {
    const regProtocal = ['https', 'http'];
    if (!link.includes(regProtocal[0]) && !link.includes(regProtocal[1])) {
      return `https://${link}`;
    }
    return link;
  }, []);

  const getIconFromName = useCallback(
    (name: Maybe<string>) => {
      const lower = name?.toLowerCase();
      const size = isCollapse ? 40 : 56;
      switch (lower) {
        case 'linkedin':
          return (
            <Icon size={size} name="linkedin_xxl" data-testid="item-linkedin" />
          );
        case 'github':
          return (
            <Icon size={size} name="github_xxl" data-testid="item-github" />
          );
        case 'twitter':
          return (
            <Icon size={size} name="twitter_xxl" data-testid="item-twitter" />
          );
        case 'medium':
          return (
            <Icon size={size} name="medium_xxl" data-testid="item-medium" />
          );
        case 'dribbble':
          return (
            <Icon size={size} name="dribbble_xxl" data-testid="item-dribble" />
          );
        case 'behance':
          return (
            <Icon size={size} name="behance_xxl" data-testid="item-behance" />
          );
        case 'instagram':
          return (
            <Icon
              size={size}
              name="instagram_xxl"
              data-testid="item-instagram"
            />
          );
        default:
          return <Icon size={size} name="my_networks_xxl" />;
      }
    },
    [isCollapse],
  );

  if (!data.link) {
    return null;
  }

  return (
    <Container
      href={makeViableLink(data.link!)}
      target="_blank"
      isCollapse={isCollapse}
    >
      {getIconFromName(getSocialNameFromLink(data.link!))}
      {!isCollapse && (
        <TextWrap>
          <TSpan
            colorToken="--text-card-neutral-default"
            overflow="ellipsis"
            font="footnote"
          >
            {handle}
          </TSpan>
        </TextWrap>
      )}
    </Container>
  );
};
