import { type FC, useCallback, useEffect, useMemo, useState } from 'react';
import { t } from 'i18next';
import * as Sentry from '@sentry/react';
import { useAuth } from '@/features/auth/contexts';
import { DeviceTypeEnum, useDeviceType } from '@/hooks';
import {
  useGenerateOneTimeTokenMutation,
  useGetTokenQuery,
  useUpdatePrivacyMutation,
} from '@/apollo/graphql';
import TokenModal from '@/screens/Money/Payments/Tokens/TokenModal';
import { Icon } from '@/components/Icon';
import { Spinner } from '@/components/Spinner';
import { useCQ } from '@/features/money/hooks';
import { Dropdown, type DropdownValueType } from '@/components/Dropdown';
import { TSpan } from '@/components/Typography';
import { Button } from '@/components/Button';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { trackEvent } from '@/utils/tracking';
import { useNoumContext } from '../../ViewChamber/ChamberProvider';
import {
  CQTokenContainer,
  GrayBox,
  TokenBoxTitle,
  GrayBoxValue,
  CQBoxTitle,
  CQFooterBox,
  HelperTextWrapper,
  WarningTextWrapper,
  RightIcon,
  PrivacyDropdownSelectedValue,
  CQBoxVisitor,
} from './styles';

export const HomeNoumCQToken: FC = () => {
  const { user } = useAuth();
  const deviceType = useDeviceType();
  const { flags } = useLaunchDarkly();
  const [openTokenModal, setOpenTokenModal] = useState(false);
  const cqURl = process.env.VITE_CQ_URL;
  const [generateOneTimeTokenMutation] = useGenerateOneTimeTokenMutation();
  const [isOpen, setIsOpen] = useState<boolean>(false);
  const [updatePrivacyMutation] = useUpdatePrivacyMutation();
  const { isOwner, space } = useNoumContext();
  const { cqData } = useCQ(space?._id || '', !isOwner);
  const { data, loading } = useGetTokenQuery();
  const isMobile = DeviceTypeEnum.MOBILE === deviceType;
  const showSingleItemOnly = !flags.cqInHomeNoumsProfile;
  const [privacy, setPrivacy] = useState<DropdownValueType<string>>({
    label: '',
    key: '',
    type: 'value',
    value: '',
  });

  useEffect(() => {
    const newData: DropdownValueType<string> = {
      ...privacy,
      key:
        (cqData?.visibility as string).charAt(0).toUpperCase() +
        (cqData?.visibility as string).slice(1),
      type: 'value',
      value:
        (cqData?.visibility as string).charAt(0).toUpperCase() +
        (cqData?.visibility as string).slice(1),
    };
    setPrivacy(newData);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [cqData]);

  const options: DropdownValueType<string>[] = [
    {
      key: 'private',
      label: (
        <TSpan
          font="body-m-bold"
          colorToken="--text-tablecell-header-neutral-highlighted"
        >
          {t(`noumena.money.cq.private`)}
        </TSpan>
      ),
      type: 'value',
      value: 'private',
    },
    {
      key: 'public',
      label: (
        <TSpan
          font="body-m-bold"
          colorToken="--text-tablecell-header-neutral-highlighted"
        >
          {t(`noumena.money.cq.public`)}
        </TSpan>
      ),
      type: 'value',
      value: 'public',
    },
  ];

  const info = useMemo(() => {
    if (data?.getSpaceByType && data?.getSpaceByType[0]) {
      return data?.getSpaceByType[0];
    }
    return {
      token: {
        count: '--',
      },
    };
  }, [data]);

  const handleNavigation = useCallback(async () => {
    const res = await generateOneTimeTokenMutation({
      variables: {},
      onError: ({ networkError = null, graphQLErrors = [] }) => {
        const [err] = graphQLErrors;
        Sentry.captureException(new Error(err?.message ?? networkError), {
          tags: {
            section: 'deleteQuestionMutation',
          },
        });
      },
      onCompleted: () => {
        trackEvent('CQStart', {
          UUID: user?._id,
        });
      },
    });
    const token = res.data?.generateOneTimeToken;
    const formedUrl: string = `${cqURl}?access_token=${token}`;
    window.open(formedUrl, '_blank');
  }, [cqURl, generateOneTimeTokenMutation, user?._id]);

  const updatePrivacy = useCallback(
    async (option) => {
      setPrivacy(option);
      await updatePrivacyMutation({
        variables: {
          input: {
            noumId: cqData.noumId || '',
            visibility: option.value.toUpperCase(),
          },
        },
        onError: ({ networkError = null, graphQLErrors = [] }) => {
          const [err] = graphQLErrors;
          Sentry.captureException(new Error(err?.message ?? networkError), {
            tags: {
              section: 'deleteQuestionMutation',
            },
          });
        },
      });
    },
    [cqData.noumId, updatePrivacyMutation],
  );

  return (
    <CQTokenContainer
      isMobile={isMobile}
      width={!isMobile && showSingleItemOnly ? '350px' : 'auto'}
    >
      {isOwner && (
        <GrayBox order={0}>
          <div
            style={{
              display: 'flex',
              flexDirection: 'column',
              width: '200px',
              height: '61px',
            }}
          >
            <TokenBoxTitle
              font="body-m"
              colorToken="--text-body-neutral-default"
            >
              {' '}
              {t('noumena.money.tokens.subheading')}
            </TokenBoxTitle>
            <GrayBoxValue
              font="heading-m"
              colorToken="--text-body-neutral-highlighted"
            >
              {' '}
              {loading ? '...' : info.token?.count}
            </GrayBoxValue>
          </div>
          <Button
            size="small"
            style={{ alignSelf: 'center' }}
            data-testid="stepTwoBackButton"
            tertiary
            textOnly
            onClick={() => setOpenTokenModal(true)}
          >
            <Icon
              name="tokensIcon"
              size={16}
              color="--icon-button-neutral-default"
            />
          </Button>
          <TokenModal
            open={openTokenModal}
            onClose={() => setOpenTokenModal(false)}
          />
        </GrayBox>
      )}
      {flags.cqInHomeNoumsProfile && (
        <>
          {isOwner ? (
            <GrayBox order={1}>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'column',
                  width: '172px',
                }}
              >
                <CQBoxTitle
                  font="body-m"
                  colorToken="--text-body-neutral-default"
                >
                  {t('noumena.money.wallet.capitalquotient')}
                </CQBoxTitle>
                {cqData.fetching && <Spinner />}
                {!cqData.fetching && (
                  <GrayBoxValue
                    font="heading-m"
                    colorToken="--text-body-neutral-highlighted"
                  >
                    {' '}
                    {cqData.score}
                  </GrayBoxValue>
                )}
                <CQFooterBox>
                  {cqData.status !== 'InComplete' && (
                    <HelperTextWrapper
                      font="body-m"
                      colorToken="--text-card-danger-primary-default"
                    >
                      {cqData.status}
                    </HelperTextWrapper>
                  )}
                  {cqData.status === 'InComplete' && (
                    <WarningTextWrapper
                      font="body-m"
                      colorToken="--text-card-neutral-highlighted"
                    >
                      {t('noumena.money.cq.incomplete_displayText')}
                    </WarningTextWrapper>
                  )}
                </CQFooterBox>
                <CQFooterBox>
                  <Dropdown
                    hideIcons
                    inputValue={
                      (privacy?.value as string).charAt(0).toUpperCase() +
                        (privacy?.value as string).slice(1) ?? ''
                    }
                    options={options}
                    usePortal={false}
                    onSelectOption={(option) => {
                      updatePrivacy(option);
                    }}
                    onOpen={() => setIsOpen(true)}
                    onClose={() => setIsOpen(false)}
                  >
                    {({ inputProps, inputRef, toggle }) => (
                      <>
                        <PrivacyDropdownSelectedValue
                          {...inputProps}
                          ref={inputRef}
                        />
                        <RightIcon
                          name="chevron_down_m"
                          size={16}
                          color="--icon-card-neutral-default"
                          isOpen={isOpen}
                          onClick={toggle}
                        />
                      </>
                    )}
                  </Dropdown>
                </CQFooterBox>
              </div>
              <Button
                size="small"
                style={{ alignSelf: 'center' }}
                data-testid="stepTwoBackButton"
                secondary
                onClick={() => handleNavigation()}
              >
                {t('noumena.money.wallet.updateCQ')}
              </Button>
            </GrayBox>
          ) : (
            <>
              {cqData?.visibility === 'Public' ? (
                <GrayBox isVisitor={!isOwner}>
                  <CQBoxVisitor>
                    <TSpan
                      font="body-m"
                      colorToken="--text-card-neutral-highlighted"
                    >
                      {t('noumena.money.wallet.capitalquotientscore')}
                    </TSpan>
                    <TSpan
                      font="heading-xs-bold"
                      colorToken="--text-card-neutral-highlighted"
                    >
                      {cqData.score}
                    </TSpan>
                  </CQBoxVisitor>
                </GrayBox>
              ) : null}
            </>
          )}
        </>
      )}
    </CQTokenContainer>
  );
};
