import { t } from 'i18next';
import React, { useCallback, useEffect, useMemo, useRef } from 'react';
import { useNavigate } from 'react-router';

import { compact } from 'lodash';
import {
  type NoumLink,
  SpaceStatusEnum,
  SpaceTypeEnum,
} from '@/apollo/generated/types';
import {
  Dropdown,
  type DropdownTargetProps,
  type DropdownValueType,
} from '@/components/Dropdown';
import { Icon } from '@/components/Icon';
import ROUTES from '@/constants/routes';
import { ContractToolRoutes } from '@/features/contracts/utils/routes';
import { useAuth } from '@/features/auth/contexts';
import { useWindowDimensions } from '@/hooks';
import { useCheckChamberCanBeUnArchived } from '@/features/money/hooks';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { ChamberUnarchiveModal } from '@/screens/Chamber/components/modals/ChamberArchive/ChamberUnarchiveModal';
import HandleUnlinkNoum from '@/screens/Chamber/components/modals/LinkNoum/HandleUnlinkNoum';
import LinkNoum from '@/screens/Chamber/components/modals/LinkNoum/LinkNoumModal';
import { type HandleUnlinkNoumRef } from '@/screens/Chamber/components/modals/LinkNoum/types';
import { useGetNoumByLinkContext } from '@/screens/Chamber/components/RightPanel/NoumByLinkProvider';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import EllipsisMenu from '@/screens/Chambers/EllipsisMenu';
import SetupNoumModal from '@/screens/Plan/SetupNoumModal';
import { Button } from '@/components/Button';
import { Stack } from '@/layout';
import { breakpoints } from '@/constants/devices';
import { useChangeProjectChamberStatusHelper } from '@/features/noums/hooks/noums';
import { useModalManager } from '@/hooks/modal/useModalManager';
import { EllipsisWrapper, NoumActionButton, RowContainer } from './styles';
import { type NoumEditorOwnerActionsProps } from './types';
import { ManageMembersModal } from '../../../modals/ManageMembersModal';
import RiseApplicationActions from '../../../ProfileSummary/RiseApplicationV2/RiseApplicationActions';

type ModalType =
  | 'link-noum'
  | 'unarchive'
  | 'setup-slot-for-noum'
  | 'manage-members';

export const NoumEditorOwnerActions: React.FC<NoumEditorOwnerActionsProps> = ({
  onEdit,
}) => {
  const navigate = useNavigate();
  const handleUnlinkNoumRef = useRef<HandleUnlinkNoumRef>(null);
  const { isActive: isUserActive } = useAuth();
  const { spaceId, isArchived, space, editDisabled, onRefetchSpaceById } =
    useNoumContext();

  const { modalType, openModal, closeModal } = useModalManager<ModalType>();

  const {
    flags: {
      paymentSubscriptions,
      invoiceTool,
      contractTool,
      elementPermission,
    },
  } = useLaunchDarkly();

  const windowSize = useWindowDimensions();

  const { noumLinkData, refetch, getLinkData, loadingLinked } =
    useGetNoumByLinkContext();

  const isMobile = useMemo(
    () => windowSize.width <= breakpoints.MOBILE_L,
    [windowSize.width],
  );

  useEffect(() => {
    getLinkData(spaceId || '');
  }, [getLinkData, spaceId]);

  const getNoumLink = useMemo(
    () => noumLinkData?.getNoumLinkByNoumId,
    [noumLinkData],
  );

  const isNoumLinked = !!getNoumLink?.link?._id;

  const { changeProjectChamberStatusHelper } =
    useChangeProjectChamberStatusHelper();

  const { checkChamberCanBeUnArchived } = useCheckChamberCanBeUnArchived();

  const handleUnarchive = useCallback(async () => {
    if (spaceId) {
      await changeProjectChamberStatusHelper(
        spaceId,
        SpaceStatusEnum.Published,
        onRefetchSpaceById,
      );
    }
  }, [spaceId, changeProjectChamberStatusHelper, onRefetchSpaceById]);

  const handleSelectDropdown = (selectedDropdown?: string) => {
    switch (selectedDropdown) {
      case 'unlinked_noum':
        if (getNoumLink?.link && getNoumLink.link.linkedNoumsCount > 2) {
          handleUnlinkNoumRef.current?.toggleUnlinkMultipleNoum();
        } else {
          handleUnlinkNoumRef.current?.toggleUnlinkConfirmationOpen();
        }
        break;
      case 'linked_noum':
        openModal('link-noum');
        break;
      case 'new_invoice':
        navigate({
          pathname: '/invoice/create',
          search: `?noumId=${spaceId}`,
        });
        break;
      case 'new_contract':
        navigate(ContractToolRoutes.createContract({ noumId: spaceId! }));
        break;
      case 'new_sow':
        navigate(
          ContractToolRoutes.createStatementOfWork({
            noumId: spaceId!,
          }),
        );
        break;
      case 'manage_members':
        openModal('manage-members');
        break;
    }
  };

  const handleGoToLinkNoum = useCallback(() => {
    if (isNoumLinked) {
      navigate(
        `${ROUTES.LINK_NOUM}?linkID=${noumLinkData?.getNoumLinkByNoumId?.link?._id}`,
      );
    } else {
      navigate(`${ROUTES.LINK_NOUM}?preselect=${spaceId}`);
    }
  }, [
    isNoumLinked,
    navigate,
    noumLinkData?.getNoumLinkByNoumId?.link?._id,
    spaceId,
  ]);

  const ellipsisMenuOptions = useMemo<DropdownValueType<string, string>[]>(
    () =>
      compact([
        elementPermission && {
          value: 'manage_members',
          key: 'Manage Members',
          type: 'value',
          label: 'Manage Members',
          icon: (
            <Icon
              name="groups_m"
              size={24}
              color="--text-tablecell-header-neutral-highlighted"
            />
          ),
        },
        {
          value: 'linked_noum',
          key: 'Linked Noum',
          type: 'value',
          label: t('noumena.link_noums.link_noums', {
            linkNo: '',
          }),
          icon: (
            <Icon
              name="link_m"
              size={24}
              color="--text-tablecell-header-neutral-highlighted"
            />
          ),
        },
        isNoumLinked && {
          value: 'unlinked_noum',
          key: 'Unlink',
          type: 'value',
          label: t('noumena.link_noums.unlink'),
          intent: 'danger',
          icon: (
            <Icon
              name="unlink_m"
              size={24}
              color="--text-tablecell-header-danger-primary-highlighted"
            />
          ),
        },
      ]),
    [elementPermission, isNoumLinked],
  );
  const PlusMenuOptions = useMemo<DropdownValueType<string, string>[]>(
    () =>
      compact([
        !!invoiceTool && {
          value: 'new_invoice',
          key: 'new_invoice',
          type: 'value',
          label: 'Invoice',
        },
        !!contractTool && {
          value: 'new_contract',
          key: 'new_contract',
          type: 'value',
          label: 'Contract',
        },
        !!contractTool && {
          value: 'new_sow',
          key: 'new_sow',
          type: 'value',
          label: 'SOW',
        },
      ]),
    [contractTool, invoiceTool],
  );

  if (isArchived) {
    return (
      <>
        <RowContainer data-testid="archived-noum-actions">
          <NoumActionButton
            disabled={editDisabled}
            size="full"
            secondary
            onClick={() =>
              checkChamberCanBeUnArchived(
                spaceId,
                () => openModal('unarchive'),
                () => openModal('setup-slot-for-noum'),
                space?.type,
              )
            }
          >
            {t('noumena.chamber.unarchive_button')}
          </NoumActionButton>
        </RowContainer>
        {paymentSubscriptions && modalType === 'setup-slot-for-noum' && (
          <SetupNoumModal
            open={modalType === 'setup-slot-for-noum'}
            onClose={closeModal}
            chamberIdAfterCreatingNoum={spaceId!}
          />
        )}
        <ChamberUnarchiveModal
          isOpen={modalType === 'unarchive'}
          handleClose={closeModal}
          onUnarchive={handleUnarchive}
        />
      </>
    );
  }

  if (space?.type === SpaceTypeEnum.RiseApplication) {
    return (
      <Stack
        gap={16}
        data-testid="rise-application-actions"
        fullWidth={isMobile}
      >
        <RiseApplicationActions />
      </Stack>
    );
  }

  return (
    <>
      <Stack gap={16} data-testid="edit-noum-actions" fullWidth={isMobile}>
        {ellipsisMenuOptions.length > 0 && (
          <EllipsisWrapper className="ellipsis-menu">
            <EllipsisMenu
              menuOptions={ellipsisMenuOptions}
              size="full"
              onClick={handleSelectDropdown}
              containerWidth="147"
              loadingLinked={loadingLinked}
            />
          </EllipsisWrapper>
        )}
        <Dropdown
          containerWidth="max-content"
          hideIcons
          closeOnSelect
          onSelectOption={(v) => handleSelectDropdown(v.value)}
          options={PlusMenuOptions}
          isAnimation={false}
          observerMinHeight="0"
          renderContainerFromBottom
        >
          {({
            targetRef,
            targetProps,
            toggle,
          }: DropdownTargetProps<HTMLButtonElement>) => (
            <Button
              ref={targetRef}
              onClick={toggle}
              {...targetProps}
              secondary
              size="large"
              leftIcon={<Icon name="plus_m" size={24} />}
            />
          )}
        </Dropdown>
        <Button
          disabled={!isUserActive}
          size={isMobile ? 'full' : 'large'}
          primary
          leftIcon={
            <Icon
              name="edit_m"
              size={24}
              color="--icon-button-neutral-alt-default"
            />
          }
          onClick={onEdit}
        >
          {t('noumena.chamber.edit_button')}
        </Button>
      </Stack>
      <LinkNoum
        goToNoumLink={handleGoToLinkNoum}
        isOpen={modalType === 'link-noum'}
        handleClose={closeModal}
      />
      <HandleUnlinkNoum
        noumLink={getNoumLink?.link as NoumLink}
        ref={handleUnlinkNoumRef}
        space={space}
        refetch={refetch}
      />

      <ManageMembersModal
        isOpen={modalType === 'manage-members'}
        handleClose={closeModal}
      />
    </>
  );
};
