import { type WrapperViewProp } from '@/screens/Chamber/components/ElementWrapper';
import { ViewMode } from '@/screens/Chamber/components/ElementWrapper/ViewMode';
import { useNetworkElement } from '../../NetworkElementProvider';
import { Container } from './styles';

export const NetworkHeader = (props: WrapperViewProp) => {
  const { spaceId, element, currentTitle } = props;
  const { isCollapse, setIsCollapse } = useNetworkElement();
  return (
    <Container data-testid="network-header-container">
      <ViewMode
        spaceId={spaceId}
        element={element}
        currentTitle={currentTitle}
        isCollapse={isCollapse}
        setIsCollapse={setIsCollapse}
      />
    </Container>
  );
};
