import {
  forwardRef,
  type Ref,
  useEffect,
  useMemo,
  useState,
  useCallback,
} from 'react';
import { t } from 'i18next';
import { endOfYesterday, format } from 'date-fns';
import { type SubmitHandler, useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { Icon } from '@/components/Icon';
import { Avatar } from '@/components/Avatar/Avatar';
import { TSpan } from '@/components/Typography';
import { TextArea } from '@/components/TextArea';
import { useAuth } from '@/features/auth/contexts';
import { useLaunchDarkly, useWindowDimensions } from '@/hooks';
import { sizes } from '@/constants/devices';
import AllAnswerModal from '@/screens/Chamber/components/elements/QuickQuestionsElement/modals/AllAnswerModal/AllAnswerModal';
import { CustomPreviewTabEnum } from '@/screens/Chamber/CustomPreview/constants';
import { UserUtil } from '@/utils/user';
import Answer, {
  AnswerElementType,
} from '@/screens/Chamber/components/elements/QuickQuestionsElement/components/Answer';
import {
  type AnswerOutput,
  QuestionStatusEnum,
} from '@/apollo/generated/types';
import { cleanList } from '@/utils/list';
import { Button } from '@/components/Button';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { useAddAnswerHelper } from '../../hooks/useAddAnswerHelper';
import EllipsisMenu from './EllipsisMenu';
import { type QuestionAnswerProps, type QuestionSchema } from './types';
import {
  Container,
  SendButton,
  StyleImage,
  SubConatiner,
  StyledTextArea,
  EllipsisButton,
  StyledForm,
  AnswerCount,
  ClosesText,
  AvatarContainer,
  QuestionBodyWrapper,
} from './styles';
import { NUM_ANSWERS_SHOWN_PER_QUESTION } from '../../constants';

export const QuestionAnswer = forwardRef(
  (
    {
      question,
      refetch,
      isCollapse,
      selectedCustomPreviewTab,
      selectedTab,
      isOwner,
    }: QuestionAnswerProps,
    ref: Ref<HTMLDivElement>,
  ) => {
    const {
      flags: { noumEditor2 },
    } = useLaunchDarkly();
    const { user } = useAuth();
    const { isConnected, isFollowing } = useNoumContext();
    const userId = user?._id;
    const {
      questionerNoumId,
      questionId,
      name,
      closesDate,
      questionBody,
      profileImageURL,
      questionImageURL,
      isQuestionOwner,
      answerCount,
      hasOwnAnswer,
      answersWithTip,
      numMoreAnswers,
    } = useMemo(() => {
      const answers = cleanList(
        question?.answers?.filter(
          (answer) =>
            answer?.tipDetails?.some((tip) => tip.tipBy?._id === userId) ||
            (answer?.user?._id === userId && !!answer?.tipDetails?.length),
        ),
      );
      return {
        questionerNoumId: question?.user?.chamber?._id,
        questionId: question?._id ?? '',
        name:
          userId === question?.user?._id
            ? t('noumena.you')
            : `${question?.user?.firstName} ${question?.user?.lastName}`,
        closesDate: question?.expiryDate,
        questionBody: question?.body,
        profileImageURL: UserUtil.getProfilePicture(question?.user),
        questionImageURL: question?.questionImage,
        isQuestionOwner: userId === question?.user?._id,
        answerCount: question?.answers?.length ?? 0,
        hasOwnAnswer: question?.answers?.find(
          (answer) => answer?.user?._id === userId,
        ),
        answersWithTip: answers.filter(
          (_item, index) => index < NUM_ANSWERS_SHOWN_PER_QUESTION,
        ),
        numMoreAnswers:
          answers.length > NUM_ANSWERS_SHOWN_PER_QUESTION
            ? answers.length - NUM_ANSWERS_SHOWN_PER_QUESTION
            : 0,
      };
    }, [question, userId]);

    const expiryDate = useMemo(
      () =>
        closesDate ? format(new Date(closesDate), 'MM/dd/yyyy') : undefined,
      [closesDate],
    );
    const hasAnswers = useMemo(() => answerCount > 0, [answerCount]);
    const isClosed = useMemo(
      () =>
        new Date(closesDate).getTime() < new Date(endOfYesterday()).getTime(),
      [closesDate],
    );

    const shouldShowAnswerField = useMemo(
      () =>
        !isClosed &&
        !isQuestionOwner &&
        !hasOwnAnswer &&
        (isOwner || (!isOwner && (isConnected || isFollowing))),
      [
        hasOwnAnswer,
        isClosed,
        isConnected,
        isFollowing,
        isOwner,
        isQuestionOwner,
      ],
    );

    const [isMobile, setIsMobile] = useState(false);
    const [isOpenAnswerModal, setIsOpenAnswerModal] = useState(false);
    const [answer, setAnswer] = useState<string>('');

    const allAnswer = () => {
      if (hasAnswers) setIsOpenAnswerModal(true);
    };

    const windowSize = useWindowDimensions();
    const MobileWidth = parseInt(sizes.MOBILE_MAX, 10);

    const divWidth = useCallback(() => {
      const checkOne =
        (isOwner && questionImageURL && isCollapse) ||
        (isQuestionOwner && questionImageURL && !isClosed);
      const checkTwo = isCollapse && questionImageURL;
      const checkThree =
        (!isCollapse && questionImageURL) ||
        (!isOwner && !isQuestionOwner) ||
        (isQuestionOwner && isClosed);

      if (checkOne) return 'auto';
      if (checkTwo) return '130px';
      if (checkThree) return 'auto';

      return '30px';
    }, [isClosed, isCollapse, isOwner, isQuestionOwner, questionImageURL]);

    const elementSchema = yup
      .object({
        answer: yup.string().required().max(600),
      })
      .required();

    const {
      handleSubmit,
      trigger,
      getValues,
      setValue,
      formState: { isValid },
    } = useForm<QuestionSchema>({
      resolver: yupResolver(elementSchema),
      mode: 'all',
      reValidateMode: 'onSubmit',
    });

    useEffect(() => {
      setValue('answer', answer);
      trigger('answer');
    }, [answer, setValue, trigger]);

    const { addAnswer, loading } = useAddAnswerHelper({ questionId, refetch });

    const onSubmit: SubmitHandler<QuestionSchema> = () =>
      addAnswer(getValues('answer'));

    useEffect(() => {
      setIsMobile(windowSize.width < MobileWidth);
    }, [MobileWidth, windowSize.width]);

    const onClickUser = useCallback(() => {
      if (
        !questionerNoumId ||
        isQuestionOwner ||
        UserUtil.isUnregistered(user) ||
        UserUtil.isUnregistered(question.user)
      )
        return;

      window.open(`/noum/${questionerNoumId}`, '_blank');
    }, [isQuestionOwner, question.user, questionerNoumId, user]);

    const handleShowMoreTips = useCallback((answerData?: AnswerOutput) => {
      setIsOpenAnswerModal(false);
      if (answerData) {
        // TODO Show the modal to display tips per answer
        // eslint-disable-next-line no-alert
        alert('Display the modal for tips per answer');
      } else {
        // TODO Show the modal to display tips for all answers
        // eslint-disable-next-line no-alert
        alert('Display the modal for tips for all answers');
      }
    }, []);

    const showAnswers =
      (selectedTab === QuestionStatusEnum.Answered ||
        selectedTab === QuestionStatusEnum.Active) &&
      hasOwnAnswer;

    return (
      <Container
        ref={ref}
        data-testid="tQuestionAnswer"
        noumEditor2={noumEditor2}
      >
        <SubConatiner vertical>
          <SubConatiner>
            <SubConatiner vertical>
              <QuestionBodyWrapper isMobile={isMobile}>
                <TSpan
                  font="body-l"
                  colorToken="--text-card-neutral-highlighted"
                >
                  {questionBody}
                </TSpan>
              </QuestionBodyWrapper>
              {questionImageURL &&
                (!isCollapse || (isCollapse && isMobile)) && (
                  <StyleImage
                    data-testid="tQINotCollapsed"
                    isCollapse={isCollapse}
                    alt={t('noumena.quick_questions.question_image')}
                    src={questionImageURL}
                  />
                )}
              {selectedTab !== QuestionStatusEnum.Tipped && (
                <SubConatiner gap="8px">
                  {!noumEditor2 && (
                    <AvatarContainer onClick={onClickUser}>
                      <Avatar url={profileImageURL ?? undefined} size="M" />
                    </AvatarContainer>
                  )}
                  <SubConatiner gap="4px">
                    <TSpan
                      font="footnote"
                      colorToken="--text-card-neutral-highlighted"
                      onClick={onClickUser}
                    >
                      {name}
                    </TSpan>
                    <TSpan
                      font="footnote"
                      colorToken="--text-card-neutral-default"
                    >
                      {' '}
                      {' '}
                    </TSpan>
                    {isClosed ? (
                      <TSpan
                        colorToken="--text-card-neutral-default"
                        font="footnote"
                      >
                        {t('noumena.quick_questions.closed')}
                      </TSpan>
                    ) : (
                      <ClosesText
                        font="footnote-bold"
                        colorToken="--text-card-neutral-highlighted"
                      >
                        {t('noumena.quick_questions.closes_on')}{' '}
                        {expiryDate ?? ''}
                      </ClosesText>
                    )}
                    <TSpan
                      font="footnote"
                      colorToken="--text-card-neutral-default"
                    >
                      {' '}
                      
                    </TSpan>{' '}
                    <AnswerCount
                      onClick={allAnswer}
                      isActive={hasAnswers}
                      font={hasAnswers ? 'link-s' : 'footnote-bold'}
                      colorToken={
                        hasAnswers
                          ? '--link-card-brand-primary-default'
                          : '--text-card-neutral-default'
                      }
                    >
                      {answerCount}{' '}
                      {t(
                        'noumena.chambers.toolbox.element.quick_questions.answers',
                      )}
                    </AnswerCount>
                  </SubConatiner>
                </SubConatiner>
              )}
            </SubConatiner>
            <SubConatiner gap="16px" width={divWidth()}>
              {questionImageURL && isCollapse && !isMobile && (
                <StyleImage
                  data-testid="tQuestionImage"
                  isCollapse={isCollapse}
                  alt={t('noumena.quick_questions.question_image')}
                  src={questionImageURL}
                />
              )}
              {selectedTab &&
                ![
                  QuestionStatusEnum.Answered,
                  QuestionStatusEnum.Tipped,
                ].includes(selectedTab) &&
                ((isQuestionOwner && !isClosed) || isOwner) &&
                selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview && (
                  <EllipsisButton
                    isAbsolute={!questionImageURL || !isCollapse || isMobile}
                  >
                    <EllipsisMenu
                      refetch={refetch}
                      questionId={questionId}
                      isOwner={isOwner}
                      isQuestionOwner={isQuestionOwner}
                      isClosed={isClosed}
                    />
                  </EllipsisButton>
                )}
              {selectedTab === QuestionStatusEnum.Tipped &&
                selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview && (
                  <EllipsisButton
                    isAbsolute={!questionImageURL || !isCollapse || isMobile}
                  >
                    <Button size="small" textOnly onClick={allAnswer}>
                      <Icon
                        size={24}
                        name="chevron_small_right_m"
                        color="--icon-button-brand-primary-default"
                      />
                    </Button>
                  </EllipsisButton>
                )}
            </SubConatiner>
          </SubConatiner>
          {showAnswers && (
            <Answer
              answer={hasOwnAnswer}
              type={AnswerElementType.MY_ANSWERS}
              isClosedQuestion={isClosed}
            />
          )}
          {selectedTab === QuestionStatusEnum.Tipped && answersWithTip.length
            ? answersWithTip.map((answerData) => (
                <Answer
                  key={answerData._id}
                  answer={answerData}
                  type={AnswerElementType.MY_TIPS}
                  isClosedQuestion={isClosed}
                />
              ))
            : null}
          {selectedTab === QuestionStatusEnum.Tipped && !!numMoreAnswers && (
            <TSpan
              style={{ margin: 'auto' }}
              cursor="pointer"
              font="button-m"
              colorToken="--text-button-brand-primary-default"
              onClick={() => handleShowMoreTips()}
            >
              {t('noumena.quick_questions.show_more_tips', {
                numTips: numMoreAnswers,
              })}
            </TSpan>
          )}
          {shouldShowAnswerField && (
            <StyledForm onSubmit={handleSubmit(onSubmit)} disabled={!isValid}>
              <SubConatiner>
                <StyledTextArea
                  value={getValues('answer')}
                  noumEditor2={noumEditor2 && !getValues('answer')}
                >
                  <TextArea
                    data-testid="tTextArea"
                    autoResize
                    value={getValues('answer')}
                    resize={false}
                    maxLength={600}
                    onChange={(e) => setAnswer(e.target.value)}
                    label={t('noumena.quick_questions.type_your_answer')}
                  />
                </StyledTextArea>
                <SendButton
                  data-testid="tSendButton"
                  type="submit"
                  textOnly
                  disabled={!isValid}
                  loading={loading}
                >
                  <Icon
                    name="send_m"
                    size={24}
                    color={
                      isValid
                        ? '--icon-button-brand-secondary-default'
                        : '--icon-button-neutral-disabled'
                    }
                  />
                </SendButton>
              </SubConatiner>
            </StyledForm>
          )}
        </SubConatiner>
        <AllAnswerModal
          question={question}
          onClose={() => setIsOpenAnswerModal(false)}
          isOpen={isOpenAnswerModal}
          onShowAllTips={handleShowMoreTips}
        />
      </Container>
    );
  },
);

export default QuestionAnswer;
