import { Button } from '@/components/Button';
import { Icon } from '@/components/Icon';
import { useNoumElement } from '@/features/noums/contexts/NoumElementContext';
import { Stack } from '@/layout';
import { NetworkStatus } from '@apollo/client';
import { type FC } from 'react';
import { useTranslation } from 'react-i18next';
import { useFileManagerElementContext } from '../../providers/FileManagerElementProvider';
import FileList from '../FileList';
import { FileManagerTabs } from '../FileManagerTabs';

const FileManagerBody: FC = () => {
  const { t } = useTranslation();
  const {
    isCollapse,
    networkStatus,
    setShowAllFilesModal,
    hasFiles,
    files,
    searchQuery,
  } = useFileManagerElementContext();

  const { isNoumEditor2 } = useNoumElement();

  return (
    <>
      {!isCollapse &&
      (hasFiles ||
        networkStatus === NetworkStatus.setVariables ||
        (!hasFiles && !!searchQuery)) ? (
        <FileManagerTabs />
      ) : null}

      <FileList maxFilesToDisplay={isNoumEditor2 ? 5 : undefined} />

      {files.length > 5 && isNoumEditor2 && (
        <Stack fullWidth justify="center">
          <Button
            textOnly
            rightIcon={
              <Icon
                name="chevron_small_right_m"
                size={24}
                color="--icon-tablecell-neutral-default"
              />
            }
            onClick={() => setShowAllFilesModal(true)}
          >
            {t('noumena.file_manager.button.all_files')}
          </Button>
        </Stack>
      )}
    </>
  );
};
export default FileManagerBody;
