import { forwardRef, type Ref, useCallback } from 'react';
import { Icon } from '@/components/Icon';
import { TSpan } from '@/components/Typography';
import { Button } from '@/components/Button';
import { useLaunchDarkly } from '@/hooks';
import { Spacer } from '@/layout';
import { CustomPreviewTabEnum } from '@/screens/Chamber/CustomPreview/constants';
import { useQuickQuestionsElementContext } from '../../providers/QuickQuestionsElementProvider';
import { ButtonView, Wrapper } from './styles';
import { type QuickQuestionsElementHeaderProps } from './types';
import { QuickQuestionsCollapseButtonTestId } from '../../testIds';

export const QuickQuestionsElementHeader = forwardRef(
  (
    {
      currentTitle = '',
      openAddQuestionModal,
      isAllowed,
      selectedCustomPreviewTab,
      openQuestionCount,
    }: QuickQuestionsElementHeaderProps,
    ref: Ref<HTMLDivElement>,
  ) => {
    const {
      flags: { noumEditor2 },
    } = useLaunchDarkly();
    const { isCollapse, setIsCollapse } = useQuickQuestionsElementContext();

    const onCollapseButtonClick = useCallback(() => {
      setIsCollapse(!isCollapse);
    }, [isCollapse, setIsCollapse]);

    return (
      <Wrapper ref={ref}>
        {!noumEditor2 && (
          <ButtonView
            data-testid={QuickQuestionsCollapseButtonTestId}
            onClick={onCollapseButtonClick}
            isBackground
          >
            <Icon
              name={isCollapse ? 'chevron_small_down_m' : 'chevron_small_up_m'}
              color="--icon-button-neutral-default"
              size={24}
            />
          </ButtonView>
        )}
        <TSpan
          font="heading-xs-bold"
          colorToken="--text-body-header-neutral-default"
        >
          {currentTitle}
        </TSpan>
        <Spacer isFlex />
        {isAllowed && (!noumEditor2 || openQuestionCount)
          ? selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview && (
              <Button
                size="small"
                type="button"
                onClick={openAddQuestionModal}
                icon={
                  <Icon
                    name="add_s"
                    size={16}
                    color={
                      noumEditor2
                        ? '--icon-button-brand-secondary-default'
                        : '--icon-button-neutral-alt-default'
                    }
                  />
                }
                primary={!noumEditor2}
                secondary={noumEditor2}
              />
            )
          : null}
      </Wrapper>
    );
  },
);
