import {
  createContext,
  type FC,
  type ReactNode,
  useContext,
  useMemo,
  useState,
} from 'react';
import { useLaunchDarkly } from '@/hooks';

export interface IQuickQuestionsElementContext {
  isCollapse: boolean;
  setIsCollapse: (value: boolean) => void;
}

export const initialValue: IQuickQuestionsElementContext = {
  isCollapse: true,
  setIsCollapse: () => {},
};

export const QuickQuestionsElementContext =
  createContext<IQuickQuestionsElementContext>(initialValue);

export const QuickQuestionsElementProvider: FC<{ children: ReactNode }> = ({
  children,
}) => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const [isCollapse, setIsCollapse] = useState(true);

  const value = useMemo(
    () => ({
      isCollapse: isCollapse && !noumEditor2,
      setIsCollapse,
    }),
    [isCollapse, noumEditor2],
  );

  return (
    <QuickQuestionsElementContext.Provider value={value}>
      {children}
    </QuickQuestionsElementContext.Provider>
  );
};

export const useQuickQuestionsElementContext = () =>
  useContext(QuickQuestionsElementContext);
