import { useCallback, useMemo, useState, useEffect } from 'react';
import { t } from 'i18next';
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from '@/components/ExtendedModal';
import { Icon } from '@/components/Icon';
import { Dropdown, type DropdownValueType } from '@/components/Dropdown';
import { TextField } from '@/components/TextField';
import { getFullName } from '@/utils/fullName';
import { Avatar } from '@/components/Avatar/Avatar';
import {
  PostVisibility,
  SortOperator,
  type UserOutput,
} from '@/apollo/generated/types';
import useDebounce from '@/hooks/useDebounce';
import { capitalizeAllWord, trimAndLowerString } from '@/utils/strings';
import { cleanList } from '@/utils/list';
import { Spacer } from '@/layout';
import { useWindowDimensions } from '@/hooks/dimensions';
import { breakpoints } from '@/constants/devices';
import VisibilityoptionsRenderer from './VisibilityOptionsRenderer';
import AuthorOptionsRenderer from './AuthorOptionsRenderer';
import { type FilterType } from '../../types';
import { type FilterPostProps } from './types';
import { OrderOptions, VisibilityOptions } from './constants';
import { usePostElement } from '../../PostElementProvider';
import {
  SortWrap,
  VisibilitySection,
  FullButton,
  EmptyButton,
  SearchSection,
} from './styles';

const allOption: DropdownValueType<string> = {
  type: 'value',
  value: 'all',
  key: 'all_authors',
  label: 'All',
};

export const FilterPost = (props: FilterPostProps) => {
  const {
    setShowFilter,
    setFilter: setPostFilter,
    authors,
    filter,
  } = usePostElement();
  const { onClose } = props;
  const [keyword, setKeyword] = useState<string>('');
  const debouncedKeyword = useDebounce<string>(keyword, 500);

  const [isOrderOpen, setIsOrderOpen] = useState(false);
  const [isVisibilityOpen, setVisibilityOpen] = useState(false);
  const [order, setOrder] = useState<SortOperator>(SortOperator.Desc);
  const [visibility, setVisibility] = useState<PostVisibility[]>([
    PostVisibility.Connection,
  ]);
  const [author, setAuthor] =
    useState<DropdownValueType<UserOutput | string>>(allOption);
  const { width } = useWindowDimensions();
  const isMobile = width < breakpoints.TABLET;

  const closeModal = useCallback(() => {
    setShowFilter(false);
  }, [setShowFilter]);

  const onSelectOrder = useCallback((op) => {
    setOrder(op.value);
  }, []);

  const onSelectMember = useCallback(
    (op) => {
      const found = visibility.find((item) => item === op.value);
      if (found) {
        setVisibility(visibility.filter((item) => item !== op.value));
      } else {
        setVisibility([...visibility, op.value]);
      }
    },
    [visibility],
  );

  const onSelectAuthor = useCallback((op) => {
    setAuthor(op);
  }, []);

  const orderValue = useMemo(
    () => OrderOptions.find((od) => od.value === order)?.label?.toString(),
    [order],
  );

  const visibilityValue = useMemo(() => {
    if (visibility.length === 2) {
      return 'All';
    }
    if (visibility.length === 0) {
      return 'Members';
    }
    return `${capitalizeAllWord(visibility[0])}s`;
  }, [visibility]);

  const authorValue = useMemo(() => {
    if (typeof author.value === 'string') {
      return 'All';
    }
    return getFullName(
      author.value.firstName,
      author.value.middleName,
      author.value.lastName,
    );
  }, [author]);

  const searchedAuthors = useMemo(() => {
    const list = authors.filter((user) => {
      const fullSearchableWord = trimAndLowerString(
        `${user.firstName || ''}${user.middleName || ''}${user.lastName || ''}`,
      );
      return fullSearchableWord.includes(trimAndLowerString(debouncedKeyword));
    });
    return list.slice(0, 20);
  }, [authors, debouncedKeyword]);

  const authorOptions = useMemo(() => {
    const options: DropdownValueType<UserOutput | string>[] = [
      {
        ...allOption,
      },
    ];
    searchedAuthors.map((item) => {
      const option: DropdownValueType<UserOutput | string> = {
        type: 'value',
        value: item,
        key: item._id,
        label: getFullName(item.firstName, item.middleName, item.lastName),
        icon: (
          <Avatar url={item.profile?.profilePictureThumbnail || ''} size="M" />
        ),
      };
      options.push(option);
      return undefined;
    });
    return options;
  }, [searchedAuthors]);

  const handleInputAuthor = useCallback((e) => {
    setKeyword(e.target.value);
  }, []);

  const applyFilters = useCallback(() => {
    const tmpFilter: FilterType = {};
    if (typeof author.value === 'string') {
      // means author is selected All
      tmpFilter.filter = {
        visibility,
      };
    } else {
      tmpFilter.filter = {
        uid: author.value._id,
        visibility,
      };
    }
    tmpFilter.sort = {
      column: 'createdAt',
      operator: order,
    };

    setPostFilter(tmpFilter);
    onClose();
  }, [author, order, visibility, setPostFilter, onClose]);

  const resetFilters = useCallback(() => {
    setPostFilter({});
  }, [setPostFilter]);

  useEffect(() => {
    if (!filter) return;
    // initialize Filter modal with Current Filter
    if (filter.filter?.uid) {
      const findAuthor = authorOptions.find((au) => {
        if (typeof au.value !== 'string') {
          return filter.filter?.uid === au.value._id;
        }
        return false;
      });
      setAuthor(findAuthor || allOption);
    } else {
      setAuthor(allOption);
    }
    setVisibility(
      cleanList(filter.filter?.visibility) || [PostVisibility.Connection],
    );
    setOrder(filter.sort?.operator || SortOperator.Desc);
  }, [filter, authorOptions]);
  return (
    <Modal
      isFullScreen={false}
      open
      testId="filter_post"
      onClose={closeModal}
      enableCloseButton
      closeButtonStyles={{ enforceRight: !isMobile }}
      style={{
        width: isMobile ? '100vw' : 450,
      }}
      disableBackdropClick
    >
      <ModalHeader
        isFullScreen={false}
        justifyContent={isMobile ? 'flex-start' : 'center'}
      >
        {t('noumena.post.filter.title')}
      </ModalHeader>

      <ModalBody
        minHeight={isMobile && width <= breakpoints.MOBILE_M ? 360 : 500}
        tabletFlex
        mobileFlex
        hideScrollbar
        isFullScreen={isMobile}
      >
        <Spacer height={12} />
        <SortWrap>
          <Dropdown
            hideIcons={true}
            closeOnSelect
            placement="bottom-end"
            options={OrderOptions}
            containerWidth="100%"
            onSelectOption={onSelectOrder}
            onOpen={() => setIsOrderOpen(true)}
            onClose={() => setIsOrderOpen(false)}
            usePortal={false}
            calRefTop={false}
            isAnimation={false}
            usePopStyle
          >
            {({ inputProps, inputRef, toggle }) => (
              <TextField
                readOnly
                inputSize="small"
                ref={inputRef}
                {...inputProps}
                value={orderValue}
                label={t('noumena.post.filter.order_label')}
                spellCheck="false"
                rightIcon={
                  isOrderOpen ? (
                    <Icon
                      name="chevron_up_m"
                      size={16}
                      color="--icon-input-neutral-default"
                      onClick={toggle}
                    />
                  ) : (
                    <Icon
                      name="chevron_down_m"
                      color="--icon-input-neutral-default"
                      size={16}
                      onClick={toggle}
                    />
                  )
                }
              />
            )}
          </Dropdown>
        </SortWrap>
        <Spacer height={24} />
        <VisibilitySection>
          <Dropdown
            multiselect
            closeOnSelect={false}
            hideIcons={true}
            placement="bottom-end"
            options={VisibilityOptions}
            containerWidth="100%"
            onSelectOption={onSelectMember}
            onOpen={() => setVisibilityOpen(true)}
            onClose={() => setVisibilityOpen(false)}
            usePortal={false}
            calRefTop={false}
            isAnimation={false}
            usePopStyle
            optionsRenderer={(options, handleSelectOption) => (
              <VisibilityoptionsRenderer
                handleSelectOption={handleSelectOption}
                options={options}
                activeItem={visibility}
              />
            )}
          >
            {({ inputProps, inputRef, toggle }) => (
              <TextField
                inputSize="small"
                readOnly
                ref={inputRef}
                {...inputProps}
                value={visibilityValue}
                label={t('noumena.post.filter.visibility_label')}
                spellCheck="false"
                rightIcon={
                  isVisibilityOpen ? (
                    <Icon
                      name="chevron_up_m"
                      color="--icon-input-neutral-default"
                      size={16}
                      onClick={toggle}
                    />
                  ) : (
                    <Icon
                      name="chevron_down_m"
                      color="--icon-input-neutral-default"
                      size={16}
                      onClick={toggle}
                    />
                  )
                }
              />
            )}
          </Dropdown>
        </VisibilitySection>
        <Spacer height={24} />
        <SearchSection>
          <Dropdown
            multiselect={false}
            hideIcons={false}
            closeOnSelect
            placement="bottom-end"
            options={authorOptions}
            containerWidth="100%"
            containerHeight={
              authorOptions.length > 2 && !isMobile ? '150px' : 'auto'
            }
            padding={authorOptions.length > 2 ? '5px 8px 5px 0' : '5px 0px'}
            onSelectOption={onSelectAuthor}
            usePortal={false}
            calRefTop={false}
            isAnimation={false}
            usePopStyle
            optionsRenderer={(options, handleSelectOption) => (
              <AuthorOptionsRenderer
                options={options}
                handleSelectOption={handleSelectOption}
                activeItem={author}
              />
            )}
          >
            {({ inputProps, inputRef, toggle }) => (
              <TextField
                inputSize="small"
                ref={inputRef}
                {...inputProps}
                value={authorValue}
                onChange={handleInputAuthor}
                label={t('noumena.post.filter.authors_label')}
                spellCheck="false"
                rightIcon={
                  <Icon
                    name="search_m"
                    size={16}
                    color="--icon-input-neutral-default"
                    onClick={toggle}
                  />
                }
              />
            )}
          </Dropdown>
        </SearchSection>
      </ModalBody>

      <ModalFooter isFullScreen={false} flexDirection="row" gap={16}>
        <EmptyButton
          onClick={resetFilters}
          color="primary"
          size="full"
          tertiary
        >
          {t('noumena.post.filter.reset_filters')}
        </EmptyButton>
        <FullButton onClick={applyFilters} primary>
          {t('noumena.post.filter.apply_filters')}
        </FullButton>
      </ModalFooter>
    </Modal>
  );
};

export default FilterPost;
