import { useCallback } from 'react';
import { t } from 'i18next';
import * as Sentry from '@sentry/react';
import { useCreateQuestionMutation } from '@/apollo/graphql';
import { useToast } from '@/hooks';
import { type HelperProps } from './types';

export function useAddQuestionHelper({
  spaceId,
  resetData,
  closeModal,
  question,
  url,
  date,
  refetch,
}: HelperProps) {
  const { addToast } = useToast();

  const [createQuestion, { loading }] = useCreateQuestionMutation();

  const handleError = useCallback(
    (error: String | Error | null) => {
      addToast('error', 'none', `${error}`);
    },
    [addToast],
  );

  const handleSuccess = useCallback(() => {
    addToast(
      'success',
      'none',
      `${t('noumena.chamber_edit.add_question.question_saved')}`,
    );
  }, [addToast]);

  const handleSaveChanges = useCallback(async () => {
    const input = {
      body: question,
      spaceId,
      questionImage: url,
      expiryDate: date?.toDateString() ?? new Date().toDateString(),
    };
    await createQuestion({
      variables: { input },
      onError: ({ networkError = null, graphQLErrors = [] }) => {
        const [err] = graphQLErrors;
        const error = t('noumena.toast_error.text');
        handleError(
          err?.message
            ? `${error}: ${err?.message}`
            : `${error}: ${networkError}`,
        );
        Sentry.captureException(new Error(err?.message ?? networkError), {
          tags: {
            section: 'createQuestionMutation',
          },
        });
      },
      onCompleted: () => {
        handleSuccess();
        if (refetch) {
          refetch();
        }
        resetData();
        closeModal();
      },
    });
  }, [
    question,
    spaceId,
    url,
    date,
    createQuestion,
    handleError,
    handleSuccess,
    refetch,
    resetData,
    closeModal,
  ]);

  return {
    loading,
    handleSaveChanges,
  };
}
