import { Icon } from '@/components/Icon';
import { TSpan } from '@/components/Typography';

import {
  CollapseButton,
  ViewModeHeaderTitleWrapper,
  ViewModeHeaderWrapper,
} from './styles';
import { type ViewModeHeaderProps } from './types';
import AddEventButton from './AddEventButton';

export const CalendarElementViewModeHeader = ({
  currentTitle,
  isCollapsed,
  isOwner,
  onToggleCollapse,
  handleDropdownClick,
  isAnotherEventLive,
  selectedCustomPreviewTab,
  onAddEvent,
  isNoumEditor,
  showAddEventBtn,
}: ViewModeHeaderProps) => (
  <>
    <ViewModeHeaderWrapper
      data-testid="calendar-element-header"
      isNoumEditor2={isNoumEditor}
    >
      {!isNoumEditor && (
        <CollapseButton
          data-testid="calendar-collapse-button"
          onClick={() => onToggleCollapse(!isCollapsed)}
        >
          <Icon
            name={!isCollapsed ? 'chevron_small_up_m' : 'chevron_small_down_m'}
            size={24}
            color="--icon-button-neutral-default"
          />
        </CollapseButton>
      )}
      <ViewModeHeaderTitleWrapper>
        <TSpan
          font="heading-xs-bold"
          colorToken="--text-body-header-neutral-default"
        >
          {currentTitle}
        </TSpan>
      </ViewModeHeaderTitleWrapper>
      {((isNoumEditor && !!showAddEventBtn) || !isNoumEditor) && (
        <AddEventButton
          isOwner={isOwner}
          handleDropdownClick={handleDropdownClick}
          isAnotherEventLive={isAnotherEventLive}
          selectedCustomPreviewTab={selectedCustomPreviewTab}
          onAddEvent={onAddEvent}
          isNoumEditor={isNoumEditor}
        />
      )}
    </ViewModeHeaderWrapper>
  </>
);
