import { useTranslation } from 'react-i18next';
import { useMemo, useState } from 'react';
import { groupBy, isEmpty } from 'lodash';
import * as Styles from '@/screens/Money/Payments/Tokens/styles';
import {
  Modal,
  ModalBody,
  ModalHeader,
  ModalSize,
} from '@/components/ExtendedModal';
import { TSpan } from '@/components/Typography';
import { Button } from '@/components/Button';
import {
  type AllCurrencyEnum,
  PaymentAccountTypeEnum,
} from '@/apollo/generated/types';
import mainWallet from '@/assets/images/main-wallet.svg';
import { useGetConnectedChamberPaymentsQuery } from '@/apollo/graphql';
import { Pagination } from '@/components/Pagination';
import { DateFormaterStringInput } from '@/screens/MoneyDetails/ViewTransactions/helper';
import convertToCurrency from '@/utils/currencyToCurrency';
import { Spacer } from '@/layout';
import { formatTimeAMPM } from '@/screens/MoneyDetails/helper';
import {
  AmountWrapper,
  Profile,
  ProfileWarpper,
  TransactionAmout,
  TransactionDetails,
  Wrapper,
  Account,
  AccountName,
} from './transactionstyles';

const TransactionWindow = (props: {
  open: boolean;
  onClose: Function;
  userId: string;
  chamberId: string;
  connectedNoumWalletId?: string;
}) => {
  const { t } = useTranslation();
  const [page, setPage] = useState<number>(1);
  const { data, loading } = useGetConnectedChamberPaymentsQuery({
    fetchPolicy: 'network-only',
    variables: {
      filter: {
        chamberId: props.chamberId || '',
        userId: props.userId,
        endDate: new Date().toISOString().split('T')[0],
        startDate: '',
        accountId: props.connectedNoumWalletId,
        accountType: [PaymentAccountTypeEnum.SubWallet],
      },
      limit: 10,
      page,
    },
  });

  const info = useMemo(() => {
    if (
      data?.getConnectedChamberPayments &&
      data?.getConnectedChamberPayments?.data
    ) {
      return data?.getConnectedChamberPayments;
    }
    return {
      data: data?.getConnectedChamberPayments?.data,
      count: data?.getConnectedChamberPayments?.count,
    };
  }, [data]);

  const onPageChange = (val: number) => {
    setPage(val);
  };

  const groupedByDay = groupBy(info?.data, (item) =>
    DateFormaterStringInput(item?.createdAt!),
  );

  return (
    <Modal
      enableCloseButton
      size={ModalSize.L}
      disableBackdropClick
      disableEscapeKeyDown
      onClose={() => props.onClose()}
      open={props.open}
    >
      {loading ? (
        <>
          <ModalBody align="center">
            <Button loading neutral />
          </ModalBody>
        </>
      ) : (
        <>
          <ModalHeader>{t(`noumena.connected_noum_transactions`)}</ModalHeader>
          <ModalBody hideScrollbar overflowY="scroll">
            {Object.keys(groupedByDay).map((key: string) => (
              <div key={key} style={{ width: '100%' }}>
                <Styles.Container>
                  <Styles.DateContainer>
                    <TSpan
                      font="body-s"
                      colorToken="--text-card-neutral-default"
                    >
                      {new Date().getUTCFullYear() === Number(key.split(',')[1])
                        ? key.split(',')[0]
                        : key}
                    </TSpan>
                  </Styles.DateContainer>
                </Styles.Container>
                {groupedByDay[key].length > 0 &&
                  groupedByDay[key as string].map((transaction) => (
                    <Wrapper key={transaction?.id}>
                      <ProfileWarpper>
                        <Profile src={mainWallet} alt="profile" />
                      </ProfileWarpper>
                      <Spacer width={16} />
                      <TransactionDetails isMobile={false}>
                        <AccountName>
                          <span style={{ fontWeight: '400' }}>
                            {t(
                              `noumena.money.money-detail.viewStatements.from`,
                            )}
                          </span>{' '}
                          {`${transaction?.sourceDetail?.name || ''} ${
                            transaction?.sourceDetail?.maskNumber || ''
                          } ${transaction?.sourceDetail?.accountName || ''}`}
                        </AccountName>
                        <Spacer height={2} />
                        <AccountName>
                          <span style={{ fontWeight: '400' }}>
                            {t(`noumena.money.money-detail.viewStatements.to`)}
                          </span>{' '}
                          {`${transaction?.destinationDetail?.name || ''}  ${
                            transaction?.destinationDetail?.accountName || ''
                          }`}
                        </AccountName>
                        <Spacer height={2} />
                        <Account>
                          {`${formatTimeAMPM(transaction?.paymentDate)} `}
                          {transaction?.transactionReason}
                        </Account>
                      </TransactionDetails>
                      <AmountWrapper>
                        <TransactionAmout isTransactionWithOwnAccounts={false}>
                          {Number(transaction?.amount) < 0 ? '-' : '+'}
                          {convertToCurrency(
                            Math.abs(Number(transaction?.amount)),
                            transaction?.currency! as AllCurrencyEnum,
                            2,
                          )}
                        </TransactionAmout>
                      </AmountWrapper>
                    </Wrapper>
                  ))}
                <Spacer height={8} />
              </div>
            ))}
            {isEmpty(groupedByDay) && (
              <TSpan
                font="body-l"
                colorToken="--text-modal-neutral-default"
                textAlign="center"
              >
                {t('noumena.money.noTransactions')}
              </TSpan>
            )}
            {info?.count! > 10 && (
              <div style={{ alignSelf: 'center' }}>
                <Spacer height={4} />
                <Pagination
                  currentPage={page}
                  pageSize={10}
                  totalCount={info.count!}
                  onPageChange={onPageChange}
                />
              </div>
            )}
          </ModalBody>
        </>
      )}
    </Modal>
  );
};

export default TransactionWindow;
