import { Fragment, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useAuth } from '@/features/auth/contexts';
import { useToggle } from '@/hooks';
import { type WrapperViewProp } from '@/screens/Chamber/components/ElementWrapper';
import { Button } from '@/components/Button';
import { SpaceTypeEnum, UserStatus } from '@/apollo/generated/types';
import { Spacer } from '@/layout';
import { getBottomStatusFromQuery, Infinite } from '@/components/Infinite';
import useScrollToLocation from '@/hooks/useScrollToLocation';
import { CustomPreviewTabEnum } from '@/screens/Chamber/CustomPreview/constants';
import { Icon } from '@/components/Icon';
import { useNavigate, useParams } from 'react-router';
import { usePermissions } from '@/features/posts/hooks';
import AuthorsList from '../AuthorsList';
import { CreatePost } from '../../modals/CreatePost/Modal';
import PostItem from '../PostItem';
import { AllPosts, Container, NoPosts, NoPostsContainer } from './styles';
import { usePostElement } from '../../PostElementProvider';
import CreateSection from '../CreateSection';
import { noPostData } from './mockData';

export const PostBody = (props: WrapperViewProp) => {
  const {
    setShowCreate,
    showCreate,
    refetchPosts,
    posts,
    loading,
    fetchMore,
    networkStatus,
    totalCount,
    space,
    isSpaceOwner,
    noumEditor2,
  } = usePostElement();
  useScrollToLocation(posts?.length > 0);
  const [showAll, toggleShowAll] = useToggle(false);
  const { isCollapse, spaceId, selectedCustomPreviewTab, isEditing } = props;
  const navigate = useNavigate();
  const { id } = useParams();
  const { user, isUnregistered } = useAuth();
  const { t } = useTranslation();
  const recentPost = useMemo(() => posts[0], [posts]);
  const getUserPermission = usePermissions();
  const isCreatable = useMemo(() => {
    if (space?.type === 'HOME' && !isSpaceOwner) return false;
    if (isUnregistered)
      return getUserPermission('POST', 'CREATE', 'UNREGISTERED');
    return (
      (isSpaceOwner || space?.isConnected) &&
      user?.userStatus === UserStatus.Active
    );
  }, [isSpaceOwner, space, user, isUnregistered, getUserPermission]);

  const noumId = id || space?._id;

  const initialSliceItems = useMemo(
    () =>
      noumEditor2 && selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview
        ? 2
        : 1,
    [noumEditor2, selectedCustomPreviewTab],
  );

  const postData = useMemo(() => {
    if (showAll) {
      return posts;
    }
    return posts.slice(0, initialSliceItems);
  }, [showAll, posts, initialSliceItems]);
  // useEffect(() => refetchPosts(), [isCollapse, refetchPosts]);
  return (
    <>
      <Container data-testid="post-body-container">
        {isCreatable &&
          selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview && (
            <CreateSection
              onClick={() => setShowCreate(true)}
              isCreatable={isCreatable ?? false}
              size={noumEditor2 ? 'L' : 'M'}
              noumEditor2={noumEditor2}
            />
          )}
        {showCreate && (
          <CreatePost
            spaceId={spaceId}
            onClose={() => setShowCreate(false)}
            onSuccess={() => {
              setShowCreate(false);
              refetchPosts();
            }}
            isMasterNoum={space?.type === 'HOME'}
          />
        )}
        {isCollapse ? (
          recentPost && (
            <PostItem
              recent
              collapsed
              data={recentPost}
              isMasterNoum={space?.type === SpaceTypeEnum.Home}
              selectedCustomPreviewTab={selectedCustomPreviewTab}
            />
          )
        ) : (
          <>
            {space?.type !== SpaceTypeEnum.Home && !isEditing && (
              <AuthorsList />
            )}
            {postData.length > 0 && (
              <AllPosts showAll={showAll}>
                <Infinite
                  width="100%"
                  onFetchMore={fetchMore}
                  unsetOverflow
                  status={
                    showAll
                      ? getBottomStatusFromQuery({
                          networkStatus,
                          totalCount,
                          currentCount: posts.length,
                        })
                      : 'end'
                  }
                >
                  {postData.map((post) => (
                    <PostItem key={`post-item-${post._id}`} data={post} />
                  ))}
                </Infinite>
              </AllPosts>
            )}
            {(!noumEditor2 ||
              selectedCustomPreviewTab === CustomPreviewTabEnum.Preview) &&
              posts.length > 1 && (
                <Button size="small" onClick={toggleShowAll} secondary>
                  {!showAll
                    ? t('noumena.post_show_all')
                    : t('noumena.post_show_less')}
                </Button>
              )}
          </>
        )}
        {!loading && posts.length < 1 && (
          <>
            {isEditing ? (
              <>
                {noPostData.map((post) => (
                  <PostItem key={`post-item-${post._id}`} data={post} />
                ))}
              </>
            ) : (
              <NoPostsContainer>
                <NoPosts font="body-m" colorToken="--text-card-neutral-default">
                  {t('noumena.post.no_posts_yet')}
                </NoPosts>
                <Spacer height={16} />
              </NoPostsContainer>
            )}
          </>
        )}
      </Container>
      {noumEditor2 &&
        selectedCustomPreviewTab !== CustomPreviewTabEnum.Preview &&
        posts.length > 2 && (
          <>
            <Spacer height={6} />
            <Button
              textOnly
              size="small"
              onClick={() => navigate(`/posts/${noumId}`)}
              secondary
              rightIcon={<Icon size={12} name="chevron_right_m" />}
            >
              {t('noumena.editor.all_posts')}
            </Button>
            <Spacer height={16} />
          </>
        )}
    </>
  );
};
