import { forwardRef, type Ref } from 'react';
import { useTranslation } from 'react-i18next';
import { ElementWrapper } from '@/screens/Chamber/components/ElementWrapper';
import { TSpan } from '@/components/Typography';

import { ElementStatusEnum } from '@/apollo/generated/types';

import { useSubWalletAccounts } from '@/features/money/hooks';
import convertToCurrency from '@/utils/currencyToCurrency';
import { EditModeWrapper } from './styles';
import { type WalletElementProps } from './types';

export const WalletElementEditMode = forwardRef(
  (props: WalletElementProps, ref: Ref<HTMLDivElement>) => {
    const { element, spaceId, isNoumEditor2 } = props;

    const { t } = useTranslation();
    const { subWalletData } = useSubWalletAccounts(
      spaceId,
      element?.tempStatus!,
    );

    return (
      <ElementWrapper isBorder={false} {...props} ref={ref}>
        <>
          <EditModeWrapper justify="center" align="center">
            <TSpan
              font="body-m"
              colorToken="--text-placeholder-neutral-default"
            >
              {element.status === ElementStatusEnum.Published
                ? `${t('noumena.money.money-detail.balanceLabel')}
                ${convertToCurrency(
                  Number(subWalletData.subWalletBalance?.value || 0),
                  subWalletData?.subWalletBalance?.currency!,
                  2,
                )}`
                : t('noumena.chamber_view.walletEditUnPublishedBalanceMode')}
            </TSpan>
          </EditModeWrapper>
          {(props.element.status === ElementStatusEnum.Draft ||
            props.element.tempStatus === ElementStatusEnum.Unsaved) &&
            isNoumEditor2 && (
              <EditModeWrapper justify="center" align="center">
                <TSpan
                  font="body-m"
                  colorToken="--text-placeholder-neutral-default"
                >
                  {t('noumena.chamber_view.walletEditUnpublishedMode')}
                </TSpan>
              </EditModeWrapper>
            )}
        </>
      </ElementWrapper>
    );
  },
);
