import { forwardRef, type Ref } from 'react';
import { useLaunchDarkly } from '@/hooks';
import QuickQuestionsElementViewMode from './QuickQuestionsElementViewMode';
import { type QuickQuestionsElementProps } from './types';
import QuickQuestionsElementEditMode from './QuickQuestionsElementEditMode';
import { QuickQuestionsElementProvider } from './providers/QuickQuestionsElementProvider';

const QuickQuestionsElement = forwardRef(
  (props: QuickQuestionsElementProps, ref: Ref<HTMLDivElement>) => {
    const {
      flags: { noumEditor2 },
    } = useLaunchDarkly();
    if ((props.isEditing && !noumEditor2) || props.isCustomPreview) {
      return <QuickQuestionsElementEditMode {...props} ref={ref} />;
    }

    return (
      <QuickQuestionsElementProvider>
        <QuickQuestionsElementViewMode {...props} />
      </QuickQuestionsElementProvider>
    );
  },
);

export default QuickQuestionsElement;
