import { Infinite } from '@/components/Infinite';
import { Spinner } from '@/components/Spinner';
import { TSpan } from '@/components/Typography';
import ChatItem from '@/features/conversation/components/ChatItem';
import { ChatItemExample } from '@/features/conversation/components/ChatItem/ChatItemExample';
import { CollapsedChatItem } from '@/features/conversation/components/ChatItem/CollapsedChatItem';
import { HomeOwnerChatItem } from '@/features/conversation/components/ChatItem/HomeOwnerChatItem';
import NewChatItem from '@/features/conversation/components/ChatItem/NewChatItem';
import { DeviceTypeEnum, useDeviceType } from '@/hooks';
import { Stack } from '@/layout';
import { useRef, type FC } from 'react';
import { useTranslation } from 'react-i18next';
import { NoumLayoutViewMode } from '@/features/conversation/types';
import { ChatListContent, ChatListWrapper, SpinnerContainer } from './styles';
import { type ChatListProps } from './types';
import useChatList from './useChatList';
import { ConversationEmptyState } from '../ConversationEmptyState';

const ChatList: FC<ChatListProps> = () => {
  const chatListDivRef = useRef<HTMLDivElement>(null);
  const newMsgSpanRef = useRef<HTMLSpanElement>(null);
  const { t } = useTranslation();
  const deviceType = useDeviceType();
  const isMobile = Boolean(deviceType === DeviceTypeEnum.MOBILE);
  const isLaptop = Boolean(deviceType === DeviceTypeEnum.LAPTOP);

  const {
    loading,
    cids,
    isNewConversation,
    isHomeUserConversation,
    isCollapse,
    showExampleConversation,
    activeConversationSid,
    noumLayoutViewMode,
    unreadCount,
    allConversations,
    handleItemClick,
    leftChannels,
    showSlicedConversations,
    infiniteStatus,
    onFetchMore,
    cidsToRender,
    hasPrivateConversation,
  } = useChatList();

  if (loading && cids.length === 0) {
    return (
      <Stack justify="center" align="center" vertical grow>
        <SpinnerContainer>
          <Spinner />
        </SpinnerContainer>
      </Stack>
    );
  }

  if (showExampleConversation) {
    return (
      <ChatItemExample
        noumLayoutViewMode={noumLayoutViewMode}
        isMobile={isMobile}
      />
    );
  }

  if (
    !isNewConversation &&
    cids.length === 0 &&
    noumLayoutViewMode !== NoumLayoutViewMode.NOUMLAYOUTCOMPACT &&
    (!isHomeUserConversation || isCollapse)
  ) {
    return <ConversationEmptyState />;
  }

  if (isCollapse) {
    return (
      <ChatListWrapper data-testid="chatlist-wrapper" isCollapse>
        <TSpan
          colorToken={
            unreadCount
              ? '--link-card-brand-primary-default'
              : '--text-body-neutral-default'
          }
          font="body-m"
          ref={newMsgSpanRef}
        >
          {t(isMobile ? 'noumena.chat.new' : 'noumena.chat.new_messages', {
            count: unreadCount || t('noumena.chat.no'),
          })}
        </TSpan>
        <ChatListContent ref={chatListDivRef} isFlex={!(isMobile || isLaptop)}>
          {allConversations.map((conversation) =>
            conversation.__typename === 'ConversationOutput' ? (
              <CollapsedChatItem
                key={conversation._id}
                sid={conversation.cid ?? ''}
                onClick={handleItemClick}
                users={conversation.participants ?? []}
              />
            ) : null,
          )}
        </ChatListContent>
        {!!leftChannels && leftChannels > 0 && (
          <TSpan colorToken="--text-tab-chips-neutral-default" font="body-m">
            +{leftChannels}
          </TSpan>
        )}
      </ChatListWrapper>
    );
  }

  return (
    <ChatListWrapper data-testid="chatlist-wrapper">
      {showSlicedConversations ? (
        <Stack vertical fullWidth align="center" overflow="initial">
          {cidsToRender.map((cid, index) => (
            <ChatItem
              index={index}
              key={cid}
              sid={cid}
              isActive={cid === activeConversationSid && !isMobile}
              isMarginRight={index !== cidsToRender.length - 1}
              onClick={handleItemClick}
              size={isMobile ? 'L' : 'S'}
            />
          ))}
        </Stack>
      ) : (
        <Infinite
          onFetchMore={onFetchMore}
          status={infiniteStatus}
          width="100%"
          scrollbarWidth={0}
          isSpinnerRelative
        >
          {!activeConversationSid && isNewConversation && <NewChatItem />}
          {isHomeUserConversation && !hasPrivateConversation && (
            <HomeOwnerChatItem isCollapsed={isCollapse} />
          )}
          {cidsToRender.map((cid, index) => (
            <ChatItem
              index={index}
              key={cid}
              sid={cid}
              isActive={cid === activeConversationSid && !isMobile}
              isMarginRight={index !== cidsToRender.length - 1}
              onClick={handleItemClick}
              size={isMobile ? 'L' : 'S'}
            />
          ))}
        </Infinite>
      )}
    </ChatListWrapper>
  );
};

export default ChatList;
