import { forwardRef, type Ref, useCallback, useState } from 'react';
import { format } from 'date-fns';
import { useTranslation } from 'react-i18next';
import { Tag } from '@/components/Tag';
import { TSpan } from '@/components/Typography';
import { Avatar } from '@/components/Avatar/Avatar';
import { useWindowDimensions } from '@/hooks/dimensions';
import riseAvatar from '@/assets/images/riseavatar.png';
import { sizes } from '@/constants/devices';
import { Button } from '@/components/Button/Button';
import { Icon } from '@/components/Icon';
import { useAuth } from '@/features/auth/contexts';
import { useToggle } from '@/hooks';
import { SpaceTypeEnum, UserStatus } from '@/apollo/generated/types';
import { PersonalDetailsModal } from '@/screens/Chamber/components/modals/PersonalDetailsModal/Modal';
import { Spacer } from '@/layout';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import RenewNoumModal from '@/screens/Plan/RenewNoumModal';
import getBgColorByNoumType from '@/utils/getBgColorByNoumType';
import getColorByNoumType from '@/utils/getColorByNoumType';
import { HomeNoumCQToken } from './HomeNoumCQToken';
import { useNoumContext } from '../../ViewChamber/ChamberProvider';
import { ProjectCreate } from '../modals/ProjectCreate/Modal';
import { ChamberCompleteness } from '../ChamberCompleteness';
import {
  TagSection,
  ProfileSummaryNameAndFavouritesContainer,
  ProfileSummaryName,
  ProfileSummaryHead,
  ProfileSummaryLocation,
  ProfileSummaryContainer,
  ProfileSummaryDescription,
  ProfileSummaryDataContainer,
  StyledCard,
  ProfileSummaryEdit,
  CompletenessWrapper,
  NoumStatusIcon,
} from './styles';
import Favourite from './Favourite';
import { useEditChamberState } from '../../EditChamber/provider';
import { type IProfileSummary } from './types';

const mobileSize = parseInt(sizes.MOBILE_L, 10);

const ProfileSummaryOld = forwardRef(
  (
    {
      disabled,
      name,
      title,
      icon,
      imageURL,
      lastUpdated,
      followers,
      location,
      bio,
      ownerName,
      ownerImageURL,
      ownerTitle,
      ownerBio,
      loading = false,
      isMasterNoum,
      isSecretNoum = false,
      spaceId,
      isUpdateMode,
      category,
      noumValidity,
      isPublished,
    }: IProfileSummary,
    ref: Ref<HTMLDivElement>,
  ) => {
    const [isRenewModalOpen, setIsRenewModalOpen] = useState(false);
    const { t } = useTranslation();
    const { isOwner, space, noumType } = useNoumContext();
    const { space: editSpace } = useEditChamberState();
    const [isEditOpen, toggleEditOpen] = useToggle(false);
    const windowSize = useWindowDimensions();
    const { flags } = useLaunchDarkly();
    const isMobile = windowSize.width <= mobileSize;
    const summaryDescription = isMasterNoum ? ownerBio : bio;
    const chamberTitle = isMasterNoum ? ownerTitle : title;
    const chamberType = chamberTitle?.toLocaleLowerCase();
    const summaryData = {
      spaceId,
      name,
      description: summaryDescription,
      profileImage: imageURL,
      categoryId: category?._id || undefined,
    };

    const { isUnregistered } = useAuth();

    const {
      aboutToExpireInfo = { showLabel: false, days: 0 },
      expiredInfo = { showLabel: false, days: 0 },
      validityDate,
    } = noumValidity || {};

    const toggleRenewModal = useCallback(() => {
      setIsRenewModalOpen((previous) => !previous);
    }, []);

    const imageAvatar =
      noumType === SpaceTypeEnum.RiseApplication ||
      space?.category?.name === 'Rise'
        ? riseAvatar
        : imageURL;

    const isRiseApplication = noumType === SpaceTypeEnum.RiseApplication;

    return (
      <ProfileSummaryContainer ref={ref}>
        {!loading && (
          <StyledCard>
            <ProfileSummaryHead data-testid="theadSection">
              <Avatar
                disabled={disabled}
                url={isMasterNoum ? ownerImageURL : imageAvatar}
                size="XXL"
                width={isMobile ? windowSize.width - 32 : undefined}
                height={isMobile ? windowSize.width - 32 : undefined}
                borderRadius={16}
              />
              <ProfileSummaryDataContainer>
                {!isMobile &&
                  !isUpdateMode &&
                  isPublished &&
                  !isUnregistered && <Favourite />}
                <TagSection data-testid="ttagsSection">
                  {chamberTitle && chamberTitle.length > 0 && (
                    <Tag
                      icon={icon ?? undefined}
                      title={chamberTitle}
                      {...(!isMasterNoum && chamberType
                        ? {
                            color: `var(${getColorByNoumType(chamberType)})`,
                            bgColor: `var(${getBgColorByNoumType(
                              chamberType,
                            )})`,
                          }
                        : { secondary: true })}
                    >
                      {isRiseApplication
                        ? t('noumena.rise_application')
                        : chamberTitle}
                    </Tag>
                  )}
                  {!isMasterNoum && !isSecretNoum && !isRiseApplication && (
                    <Tag tertiary>
                      {t(`noumena.profile_summary.followers`, {
                        postProcess: 'interval',
                        count: followers || 0,
                      })}
                    </Tag>
                  )}
                  {disabled && <Tag tertiary>{t(`noumena.archived`)}</Tag>}
                  {aboutToExpireInfo.showLabel && (
                    <Tag
                      secondary
                      icon={<NoumStatusIcon name="time_m" size={16} />}
                    >
                      {`${t('noumena.money.subscription.noum.expires.text', {
                        day: aboutToExpireInfo.days,
                      })} `}
                      {isOwner && (
                        <TSpan
                          colorToken="--text-tag-brand-primary-default"
                          font="link-m"
                          onClick={toggleRenewModal}
                        >
                          {`${t('noumena.money.myplans.renew')}`}
                        </TSpan>
                      )}
                    </Tag>
                  )}
                  {expiredInfo.showLabel && !!validityDate && (
                    <Tag
                      danger
                      icon={<NoumStatusIcon name="time_m_red" size={16} />}
                    >
                      {expiredInfo.showGracePeriodLabel
                        ? `${t(
                            `noumena.money.subscription.noum.grace.period.text`,
                            {
                              day: expiredInfo.days,
                            },
                          )} `
                        : `${t('noumena.money.subscription.noum.expired.text', {
                            date: format(new Date(validityDate), 'dd MMM yyyy'),
                          })} `}
                      {isOwner && (
                        <TSpan
                          colorToken="--text-tag-brand-primary-default"
                          font="link-m"
                          onClick={toggleRenewModal}
                        >
                          {`${t('noumena.money.myplans.renew')}`}
                        </TSpan>
                      )}
                    </Tag>
                  )}
                </TagSection>
                <ProfileSummaryNameAndFavouritesContainer>
                  <ProfileSummaryName
                    disabled={disabled}
                    data-testid="tprofileSummaryName"
                  >
                    {isMasterNoum ? ownerName : name}
                  </ProfileSummaryName>
                  {isMobile &&
                    !isUpdateMode &&
                    isPublished &&
                    !isUnregistered && <Favourite />}
                </ProfileSummaryNameAndFavouritesContainer>
                {isMasterNoum && location ? (
                  <ProfileSummaryLocation data-testid="tprofileSummaryLocation">
                    {location}
                  </ProfileSummaryLocation>
                ) : null}
                <TSpan
                  font="body-m"
                  $fill
                  colorToken="--text-card-neutral-default"
                >
                  {disabled
                    ? t(`noumena.profile_summary.archived`, { lastUpdated })
                    : lastUpdated
                    ? t(`noumena.profile_summary.last_update`, {
                        lastUpdated,
                      })
                    : null}
                </TSpan>
              </ProfileSummaryDataContainer>
              {isUpdateMode && (
                <ProfileSummaryEdit>
                  <Button
                    leftIcon={
                      <Icon
                        name="edit_m"
                        size={24}
                        color="--icon-button-neutral-default"
                      />
                    }
                    size="small"
                    tertiary
                    onClick={toggleEditOpen}
                    disabled={
                      editSpace?.category?.name.toLowerCase() ===
                      'rise_application'
                    }
                  >
                    {t(`noumena.chamber.edit_button`)}
                  </Button>
                </ProfileSummaryEdit>
              )}
            </ProfileSummaryHead>
            <ProfileSummaryDescription
              data-testid="tprofileSummaryDescription"
              isMobile={summaryDescription?.length ? isMobile : true}
            >
              {summaryDescription}
            </ProfileSummaryDescription>
            {isMasterNoum && flags.cqInHomeNoumsProfile && (
              <>
                <Spacer height={24} />
                <HomeNoumCQToken />
              </>
            )}
            {isMasterNoum &&
            isOwner &&
            space?.uid?.userStatus === UserStatus.Active &&
            (!space?.percentCompleted || space?.percentCompleted < 100) ? (
              <CompletenessWrapper>
                <ChamberCompleteness />
              </CompletenessWrapper>
            ) : null}
          </StyledCard>
        )}
        <>
          {isMasterNoum ? (
            <PersonalDetailsModal
              isOpen={isEditOpen}
              handleClose={toggleEditOpen}
              handleSuccess={toggleEditOpen}
              spaceId={spaceId}
              isUpdateMode
            />
          ) : isEditOpen ? (
            <ProjectCreate
              isOpen={isEditOpen}
              handleClose={toggleEditOpen}
              handleSuccess={toggleEditOpen}
              isUpdateMode
              summaryData={summaryData}
            />
          ) : null}
        </>
        {isRenewModalOpen && flags.paymentSubscriptions && isOwner && (
          <RenewNoumModal
            onClose={toggleRenewModal}
            open={isRenewModalOpen}
            chamber_id={noumValidity?.chember}
            noumExpiryDate={noumValidity?.validityDate}
            noum_transaction_fee_id={noumValidity?.noumTransactionFeeId}
          />
        )}
      </ProfileSummaryContainer>
    );
  },
);

export default ProfileSummaryOld;
