import { useEffect, useMemo, useState, type RefObject } from 'react';

import useResizeObserver from '@/hooks/useResizeObserver';
import { useLaunchDarkly } from '@/hooks';

export const useCalendar = (
  childrenRef: RefObject<HTMLDivElement>,
  isCustomPreview: boolean,
) => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const [isCollapsed, setIsCollapsed] = useState<boolean>(
    !(noumEditor2 && !isCustomPreview),
  );
  const [viewMode, setViewMode] = useState<string>('list');
  const [childrenHeight, setChildrenHeight] = useState<number>();

  useResizeObserver(childrenRef, () => {
    setChildrenHeight(childrenRef?.current?.scrollHeight || 0);
  });

  const isNoumEditor2 = useMemo(
    () => noumEditor2 && !isCustomPreview,
    [isCustomPreview, noumEditor2],
  );

  useEffect(() => {
    if (!childrenHeight) {
      setChildrenHeight(childrenRef.current?.clientHeight);
    }
  }, [childrenHeight, childrenRef]);

  return {
    isCollapsed,
    setIsCollapsed,
    viewMode,
    setViewMode,
    childrenHeight,
    isNoumEditor2,
  };
};
