import { EventItemButtons } from '@/features/events/components';
import { getLocalTimeFormatted } from '@/utils/date';
import { t } from 'i18next';
import {
  withRecurringEvent,
  type WithRecurringEventProps,
} from '../modals/EventListModal/withRecurringEvent';
import {
  EventDateWrapper,
  GuestEventBody,
  GuestEventBodyWrapper,
  GuestEventDate,
  GuestEventMonth,
  GuestEventTime,
  GuestEventTitle,
  GuestEventsHeaderTitle,
  GuestEventsListCollapsedItem,
  GuestEventsListCollapsedWrapper,
} from './styles';
import { type CalendarGuestEventsListCollapsedProps } from './types';

export const CalendarGuestEventsListCollapsed = withRecurringEvent(
  ({
    events,
    eventHost,
    masterNoumId,
    onEditEvent,
    onGoLive,
    onJoinEvent,
    onAccept,
    onDecline,
    onAttend,
    onAttending,
    onNotAttending,
    onEventDeclined,
    onEventAccepted,
  }: CalendarGuestEventsListCollapsedProps &
    Partial<WithRecurringEventProps>) => (
    <GuestEventsListCollapsedWrapper data-testid="guest-events-list-collapsed">
      {!!eventHost && (
        <GuestEventsHeaderTitle
          font="body-m"
          colorToken="--text-card-neutral-highlighted"
        >
          {t('noumena.homeChambers.event.guest_upcoming_events_num', {
            postProcess: 'interval',
            count: events?.length || 0,
            host: eventHost,
          })}
        </GuestEventsHeaderTitle>
      )}
      {events.map((event) => (
        <GuestEventsListCollapsedItem key={event._id}>
          <EventDateWrapper>
            <GuestEventMonth colorToken="--text-upcoming-event-brand-primary-highlighted">
              {getLocalTimeFormatted({
                dateTime: event.eventDate,
                returnFormat: 'MMMM',
              })}
            </GuestEventMonth>
            <GuestEventDate colorToken="--text-upcoming-event-neutral-highlighted">
              {getLocalTimeFormatted({
                dateTime: event.eventDate,
                returnFormat: 'd',
              })}
            </GuestEventDate>
          </EventDateWrapper>
          <GuestEventBodyWrapper>
            <GuestEventBody>
              <GuestEventTitle colorToken="--text-upcoming-event-neutral-highlighted">
                {event.title}
              </GuestEventTitle>
              <GuestEventTime colorToken="--text-tablecell-body-neutral-default">
                {getLocalTimeFormatted({
                  dateTime: event.eventDate,
                  returnFormat: 'h:mm aa',
                })}
              </GuestEventTime>
            </GuestEventBody>
            <EventItemButtons
              type="calendar"
              event={event}
              noumId={masterNoumId ?? ''}
              onEditEvent={() => onEditEvent(event)}
              onGoLive={() => onGoLive(event)}
              onJoinEvent={() => onJoinEvent(event)}
              onAccept={() => onEventAccepted?.(event, onAccept)!}
              onDecline={() => onEventDeclined?.(event, onDecline)!}
              onAttend={() => onEventAccepted?.(event, onAttend)!}
              onAttending={() => onEventAccepted?.(event, onAttending)!}
              onNotAttending={() => onEventDeclined?.(event, onNotAttending)!}
            />
          </GuestEventBodyWrapper>
        </GuestEventsListCollapsedItem>
      ))}
    </GuestEventsListCollapsedWrapper>
  ),
);
