import { Button } from '@/components/Button';
import { Icon } from '@/components/Icon';
import { useNoumElement } from '@/features/noums/contexts/NoumElementContext';
import { useBreakpoints } from '@/hooks';
import { ViewMode } from '@/screens/Chamber/components/ElementWrapper/ViewMode';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { NetworkStatus } from '@apollo/client';
import { useFileManagerElementContext } from '../../providers/FileManagerElementProvider';
import { FileManagerSearch } from '../FileManagerSearch/FileManagerSearch';
import { AddButton, Container, HeaderRightWrapper } from './styles';

type FileManagerHeaderProps = {};

const FileManagerHeader: React.FC<FileManagerHeaderProps> = () => {
  const { t } = useTranslation();
  const { isMobile } = useBreakpoints();
  const {
    setShowAddFileModal,
    isCollapse,
    setIsCollapse,
    spaceId,
    element,
    currentTitle,
    files,
    hasFiles,
    isEditing,
    searchQuery,
    networkStatus,
  } = useFileManagerElementContext();

  const { isNoumEditor2 } = useNoumElement();

  const handleClickSeeAll = () => {
    setIsCollapse(false);
  };

  const showSearch =
    !isCollapse &&
    (hasFiles || !!searchQuery || networkStatus === NetworkStatus.setVariables);

  const totalCount = files.length;
  const showAddButton = !isEditing && (hasFiles || !!searchQuery);

  return (
    <Container>
      <ViewMode
        spaceId={spaceId}
        element={element}
        currentTitle={currentTitle}
        isCollapse={isCollapse}
        setIsCollapse={setIsCollapse}
        fullWidth={!isNoumEditor2 || !isMobile}
      />
      <HeaderRightWrapper>
        {showSearch ? <FileManagerSearch /> : null}
        {isCollapse && totalCount > 2 && (
          <Button onClick={handleClickSeeAll} textOnly>
            {t('noumena.file_manager.header.see_all', { count: totalCount })}
          </Button>
        )}
        {showAddButton && (
          <AddButton
            data-testid="add-button"
            onClick={() => setShowAddFileModal(true)}
            size="small"
            primary={!isNoumEditor2}
            secondary={isNoumEditor2}
            icon={
              <Icon
                name="add_s"
                size={16}
                color={
                  isNoumEditor2
                    ? '--icon-button-brand-secondary-default'
                    : '--icon-button-neutral-alt-default'
                }
              />
            }
          />
        )}
      </HeaderRightWrapper>
    </Container>
  );
};

export default FileManagerHeader;
