import { PermissibleElementType } from '@/apollo/generated/types';
import { Button } from '@/components/Button';
import { Icon } from '@/components/Icon';
import { TSpan } from '@/components/Typography';
import { useAuth } from '@/features/auth/contexts';
import { ActiveConversationContext } from '@/features/conversation/contexts/ActiveConversationContext';
import ConversationHooks from '@/features/conversation/hooks/globalMessages';
import { ViewMode } from '@/features/conversation/types';
import { useNoumAuthorization } from '@/features/noums/contexts/NoumAuthorizationContext';
import { useBreakpoints, useLaunchDarkly } from '@/hooks';
import { Spacer } from '@/layout';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { UserUtil } from '@/utils/user';
import React, {
  forwardRef,
  useCallback,
  useContext,
  useLayoutEffect,
  useMemo,
  type Ref,
} from 'react';
import { useTranslation } from 'react-i18next';
import { MessageElementContext } from '../contexts/MessageElementProvider';
import FilterDropDown from './FilterDropDown';
import { Wrapper } from './styles';
import { type ChatheaderProps } from './types';

export const ChatHeader = forwardRef(
  ({ currentTitle = '' }: ChatheaderProps, ref: Ref<HTMLDivElement>) => {
    const {
      flags: { noumEditor2 },
    } = useLaunchDarkly();
    const { user } = useAuth();
    const { t } = useTranslation();
    const { isMasterNoum, isOwner } = useNoumContext();
    const { isMobile } = useBreakpoints();
    const { hasElementPermission } = useNoumAuthorization();

    const {
      isCollapse,
      setIsCollapse,
      isNewConversation,
      setIsNewConversation,
      viewMode,
      setViewMode,
      conversationType,
      noumLayoutViewMode,
      isNoumLayoutCompactViewMode,
    } = useContext(MessageElementContext);
    const { activeConversationSid, setActiveConversationSid } = useContext(
      ActiveConversationContext,
    );
    const { count } = ConversationHooks.useConversationsList(conversationType);

    const hasCreateConversationPermission =
      hasElementPermission(
        PermissibleElementType.Messages,
        'create-new-message-conversation',
      ) || isMasterNoum;

    const isCollapseDisabled =
      !activeConversationSid &&
      !isOwner &&
      !isMasterNoum &&
      !isNewConversation &&
      count === 0;
    const isShowFilter =
      !isCollapse && !isMasterNoum && isOwner && !isNoumLayoutCompactViewMode;

    const isShowAddButton = useMemo(
      () => !UserUtil.isInactive(user) && (!isMasterNoum || isOwner),
      [isMasterNoum, isOwner, user],
    );

    const collapseHandler = useCallback(
      (e: React.MouseEvent<HTMLButtonElement>) => {
        if (isCollapseDisabled) {
          e.preventDefault();
        } else {
          setIsCollapse(!isCollapse);
        }
      },
      [isCollapse, setIsCollapse, isCollapseDisabled],
    );

    const backHandler = useCallback(() => {
      setViewMode(ViewMode.DEFAULT);
    }, [setViewMode]);

    const onAddNewConversationHandle = useCallback(
      (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
        e.stopPropagation();

        setIsNewConversation(true);
        setActiveConversationSid('');
      },
      [setActiveConversationSid, setIsNewConversation],
    );

    useLayoutEffect(() => {
      setIsCollapse(isCollapse || isCollapseDisabled);
    }, [isCollapseDisabled, isCollapse, setIsCollapse]);

    return (
      <Wrapper ref={ref} noumLayoutViewMode={noumLayoutViewMode}>
        {viewMode === ViewMode.FULLCHAT ? (
          <Button
            data-testid="back-button"
            neutral
            size="small"
            icon={
              <Icon
                name="arrow_left_m"
                size={24}
                color="--icon-button-neutral-default"
              />
            }
            onClick={backHandler}
          />
        ) : (
          <>
            {!noumEditor2 && (
              <Button
                data-testid="collapse-button"
                tertiary
                icon={
                  isCollapse ? (
                    <Icon
                      name="chevron_small_down_m"
                      size={24}
                      color={
                        isCollapseDisabled
                          ? '--icon-button-neutral-disabled'
                          : '--icon-button-neutral-default'
                      }
                    />
                  ) : (
                    <Icon
                      name="chevron_small_up_m"
                      size={24}
                      color={
                        isCollapseDisabled
                          ? '--icon-button-neutral-disabled'
                          : '--icon-button-neutral-default'
                      }
                    />
                  )
                }
                disabled={!!isCollapseDisabled}
                size="small"
                onClick={collapseHandler}
              />
            )}
          </>
        )}
        {!isNoumLayoutCompactViewMode && (
          <TSpan
            font="heading-xs-bold"
            colorToken="--text-body-header-neutral-default"
          >
            {currentTitle}
          </TSpan>
        )}
        {!isNoumLayoutCompactViewMode && <Spacer isFlex />}
        {isShowFilter && <FilterDropDown />}
        {isShowAddButton && (
          <Button
            data-testid="new-conversation-button"
            size="small"
            disabled={!hasCreateConversationPermission}
            tooltipText={
              hasCreateConversationPermission
                ? ''
                : t('noumena.chat.no_permission.create_conversation_text')
            }
            tooltipPosition={
              isMobile
                ? 'top-left'
                : isNoumLayoutCompactViewMode
                ? 'top-right'
                : 'top-center'
            }
            icon={
              <Icon
                name="add_s"
                size={16}
                color={
                  noumEditor2
                    ? '--icon-button-brand-secondary-default'
                    : '--icon-button-neutral-alt-default'
                }
              />
            }
            primary={!noumEditor2}
            secondary={noumEditor2}
            onClick={onAddNewConversationHandle}
          />
        )}
      </Wrapper>
    );
  },
);
