import { format } from 'date-fns';
import { MemberRoleTag } from '@/features/noums/components/MemberRoleTag';
import { Tag } from '@/components/Tag';
import { TSpan } from '@/components/Typography';
import getColorByNoumType from '@/utils/getColorByNoumType';
import getBgColorByNoumType from '@/utils/getBgColorByNoumType';
import { SpaceTypeEnum } from '@/apollo/generated/types';
import { useAuth } from '@/features/auth/contexts';
import { useTranslation } from 'react-i18next';
import { type NoumMemberQuery } from '@/apollo/graphql';
import { NoumStatusIcon } from './styles';
import { CQToken } from './CQToken';
import { useNoumContext } from '../../ViewChamber/ChamberProvider';
import { type INoumValidity } from '../../ViewChamber/types';

export interface NoumTagsDetailProps {
  isMasterNoum: boolean | undefined;
  noumValidity: INoumValidity | undefined;
  disabled: boolean | undefined;
  memberData: NoumMemberQuery | undefined;
  ownerTitle: string | undefined;
  title: string | undefined;
  icon: JSX.Element | undefined;
  toggleRenewModal: () => void;
}

const NoumTagsDetail = ({
  isMasterNoum,
  noumValidity,
  disabled,
  memberData,
  ownerTitle,
  title,
  icon,
  toggleRenewModal,
}: NoumTagsDetailProps) => {
  const { noumType, space } = useNoumContext();
  const isRiseApplication = noumType === SpaceTypeEnum.RiseApplication;
  const { user } = useAuth();
  const isOwner = user?._id === space?.uid?._id;
  const chamberTitle = isMasterNoum ? ownerTitle : title;
  const chamberType = chamberTitle?.toLocaleLowerCase();
  const { t } = useTranslation();

  const {
    aboutToExpireInfo = { showLabel: false, days: 0 },
    expiredInfo = { showLabel: false, days: 0 },
    validityDate,
  } = noumValidity || {};

  return (
    <>
      {isMasterNoum && <CQToken />}
      {memberData?.noumMember && (
        <MemberRoleTag member={memberData.noumMember} />
      )}
      {!isMasterNoum && chamberTitle && chamberTitle.length > 0 && (
        <Tag
          icon={icon}
          title={chamberTitle}
          {...(!isMasterNoum && chamberType
            ? {
                color: `var(${getColorByNoumType(chamberType)})`,
                bgColor: `var(${getBgColorByNoumType(chamberType)})`,
              }
            : { secondary: true })}
          size="small"
        >
          {isRiseApplication ? t('noumena.rise_application') : chamberTitle}
        </Tag>
      )}
      {!isMasterNoum && !isRiseApplication && space?.projectType && (
        <Tag tertiary size="small">
          {t(`noumena.noum.project_type.${space?.projectType}`)}
        </Tag>
      )}

      {disabled && <Tag tertiary>{t(`noumena.archived`)}</Tag>}
      {aboutToExpireInfo.showLabel && (
        <Tag secondary icon={<NoumStatusIcon name="time_m" size={16} />}>
          {`${t('noumena.money.subscription.noum.expires.text', {
            day: aboutToExpireInfo.days,
          })} `}
          {isOwner && (
            <TSpan
              colorToken="--text-tag-brand-primary-default"
              font="link-m"
              onClick={toggleRenewModal}
            >
              {`${t('noumena.money.myplans.renew')}`}
            </TSpan>
          )}
        </Tag>
      )}
      {expiredInfo.showLabel && !!validityDate && (
        <Tag danger icon={<NoumStatusIcon name="time_m_red" size={16} />}>
          {expiredInfo.showGracePeriodLabel
            ? `${t(`noumena.money.subscription.noum.grace.period.text`, {
                day: expiredInfo.days,
              })} `
            : `${t('noumena.money.subscription.noum.expired.text', {
                date: format(new Date(validityDate), 'dd MMM yyyy'),
              })} `}
          {isOwner && (
            <TSpan
              colorToken="--text-tag-brand-primary-default"
              font="link-m"
              onClick={toggleRenewModal}
            >
              {`${t('noumena.money.myplans.renew')}`}
            </TSpan>
          )}
        </Tag>
      )}
    </>
  );
};

export default NoumTagsDetail;
