import { useMemo, type FC } from 'react';
import { useTranslation } from 'react-i18next';
import {
  type AllCurrencyEnum,
  type TransferDetail,
} from '@/apollo/generated/types';
import mainWallet from '@/assets/images/main-wallet.svg';

import convertToCurrency from '@/utils/currencyToCurrency';
import { DeviceTypeEnum, useDeviceType, useLaunchDarkly } from '@/hooks';
import { Separator } from '@/components/Separator/Separator';
import { Stack } from '@/layout';
import {
  Wrapper,
  Profile,
  ProfileWarpper,
  TransactionDetails,
  AccountName,
  Account,
  TransactionAmout,
  TransactionContent,
} from './styles';

interface Transaction {
  sourceDetail: TransferDetail | undefined;
  destinationDetail: TransferDetail | undefined;
  paymentDate: string | undefined | null;
  amount: number | undefined | null;
  transactionReason: string | undefined | null;
  currency: string | undefined | null;
  hideTo?: boolean;
  columnWidth?: number;
}

const TransactionCard: FC<Transaction> = ({
  sourceDetail,
  destinationDetail,
  paymentDate,
  amount,
  transactionReason,
  currency,
  hideTo = false,
  columnWidth,
}) => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const formatTimeAMPM = (str: string | undefined | null) => {
    if (!str) return '';
    const date = new Date(Number(str));
    let hours = date.getHours();
    const minutes = date.getMinutes();
    const ampm = hours >= 12 ? 'PM' : 'AM';
    hours %= 12;
    hours = hours || 12; // the hour '0' should be '12'
    const newminutes = `0${minutes}`.slice(-2);
    const strTime = `${hours}:${newminutes} ${ampm}`;
    return strTime;
  };
  const { t } = useTranslation();
  const deviceType = useDeviceType();

  const contentMaxWidth = useMemo(
    () => (noumEditor2 && columnWidth ? columnWidth - 100 - 32 : undefined),
    [columnWidth, noumEditor2],
  );

  return (
    <Stack fullWidth vertical padding="0 16px">
      <Wrapper justify="space-between">
        <TransactionContent
          gap={16}
          fullWidth={!contentMaxWidth}
          align="center"
          maxWidth={contentMaxWidth}
        >
          <ProfileWarpper>
            <Profile src={mainWallet} alt="profile" />
          </ProfileWarpper>
          <TransactionDetails>
            <AccountName>
              <span style={{ fontWeight: '400' }}>
                {t(`noumena.money.money-detail.viewStatements.from`)}
              </span>{' '}
              {`${sourceDetail?.name || ''} ${sourceDetail?.maskNumber || ''} ${
                sourceDetail?.accountName || ''
              }`}
            </AccountName>
            {!hideTo && (
              <AccountName>
                <span style={{ fontWeight: '400' }}>
                  {t(`noumena.money.money-detail.viewStatements.to`)}
                </span>{' '}
                {`${destinationDetail?.name || ''}  ${
                  destinationDetail?.accountName || ''
                }`}
              </AccountName>
            )}
            <Account>
              {`${formatTimeAMPM(paymentDate)} `}
              {transactionReason}
            </Account>
          </TransactionDetails>
        </TransactionContent>
        <TransactionAmout
          isMobile={DeviceTypeEnum.MOBILE === deviceType}
          noumEditor2={noumEditor2}
        >
          {Number(amount) < 0 ? '-' : '+'}
          {convertToCurrency(
            Math.abs(Number(amount)),
            currency! as AllCurrencyEnum,
            2,
          )}
        </TransactionAmout>
      </Wrapper>
      <Separator fullWidth noMargin />
    </Stack>
  );
};
export default TransactionCard;
