import { type WrapperViewProp } from '@/screens/Chamber/components/ElementWrapper';
import { ViewMode } from '@/screens/Chamber/components/ElementWrapper/ViewMode';
import { Icon } from '@/components/Icon';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { ViewModeNoumEditor } from '@/screens/Chamber/components/ElementWrapper/ViewModeNoumEditor';
import { Container, FilterButton } from './styles';
import { usePostElement } from '../../PostElementProvider';
import { FilterPost } from '../../modals/FilterPost';

const PostHeader = (props: WrapperViewProp) => {
  const {
    spaceId,
    element,
    currentTitle,
    isCollapse,
    setIsCollapse,
    isCustomPreview,
  } = props;
  const { setShowFilter, showFilter, isCollapseDisabled, noumEditor2 } =
    usePostElement();
  const { space } = useNoumContext();

  return (
    <Container noumEditor2={noumEditor2 && !isCustomPreview}>
      {noumEditor2 && !isCustomPreview ? (
        <ViewModeNoumEditor currentTitle={currentTitle} />
      ) : (
        <>
          <ViewMode
            spaceId={spaceId}
            element={element}
            currentTitle={currentTitle}
            isCollapse={isCollapse}
            setIsCollapse={setIsCollapse}
            isCollapseDisabled={isCollapseDisabled}
          />
          {!isCollapse && space?.type !== 'HOME' && (
            <FilterButton
              data-testid="filter-button"
              onClick={() => setShowFilter(true)}
              size="small"
              icon={
                <Icon
                  name="align_center_m"
                  size={24}
                  color="--icon-button-neutral-default"
                />
              }
            />
          )}

          {showFilter && <FilterPost onClose={() => setShowFilter(false)} />}
        </>
      )}
    </Container>
  );
};

export default PostHeader;
