import { useMemo } from 'react';
import { Badge } from '@/components/Badge/Badge';

import { useAuth } from '@/features/auth/contexts';
import { type Maybe, type UserOutput } from '@/apollo/generated/types';
import { cleanList } from '@/utils/list';
import * as S from './styles';

import { ChatItemAvatar } from './ChatItemAvatar';
import ConversationHooks from '../../hooks/globalMessages';
import { mapToUserData } from '../../helpers';

type CollapsedChatItemProps = {
  sid: string;
  isMarginRight?: boolean;
  onClick: (sid: string) => void;
  size?: 'S' | 'M' | 'L';
  users: Maybe<UserOutput>[];
};

export const CollapsedChatItem = ({
  sid,
  isMarginRight = true,
  onClick,
  size,
  users = [],
}: CollapsedChatItemProps) => {
  const { user: currentUser } = useAuth();
  const unread = ConversationHooks.useConversationUnreadMessageCount({ sid });

  const participants = useMemo(
    () =>
      cleanList(
        users
          .filter((participant) => participant?._id !== currentUser?._id)
          .map((participant) => participant && mapToUserData(participant)),
      ),
    [currentUser?._id, users],
  );

  return (
    <S.ChatAvatarWrapper
      data-testid="chatavatar-wrapper"
      isMarginRight={isMarginRight}
      onClick={() => onClick(sid)}
    >
      <ChatItemAvatar users={participants} size={size} />
      {unread !== 0 && (
        <S.BadgeWrapper>
          <Badge text={`${unread}`} />
        </S.BadgeWrapper>
      )}
    </S.ChatAvatarWrapper>
  );
};
