import { Icon } from '@/components/Icon';
import { TSpan } from '@/components/Typography';
import { t } from 'i18next';
import {
  PROFILE_EDIT_BUTTON,
  PROFILE_EDIT_ICON_POSITION,
  PROFILE_EDIT_ICON_RADIUS,
  PROFILE_EDIT_ICON,
} from './constants';
import { StyledButton } from './styles';
import { type ProfileBannerEditIconProps, ProfileSize } from './types';

export const BannerEditButton = ({
  name,
  size,
  color,
  onClick,
  isBanner,
}: ProfileBannerEditIconProps) => {
  const buttonSize = PROFILE_EDIT_BUTTON[size || ProfileSize.L];
  const position = isBanner
    ? 16
    : PROFILE_EDIT_ICON_POSITION[size || ProfileSize.L];
  const radius = isBanner ? 8 : PROFILE_EDIT_ICON_RADIUS[size || ProfileSize.L];
  const iconSize = isBanner ? 24 : PROFILE_EDIT_ICON[size || ProfileSize.L];

  return (
    <StyledButton
      data-testid="avatarEditButton"
      size={buttonSize}
      position={position}
      radius={radius}
      onClick={onClick}
      isBanner={isBanner}
    >
      <Icon
        name={name}
        size={iconSize}
        color={color}
        data-testid="avatarEditButtonIcon"
      />
      {isBanner && (
        <TSpan font="button-s" colorToken="--bg-card-neutral-alt-default">
          {t(`noumena.avatar.add_button_text`)}
        </TSpan>
      )}
    </StyledButton>
  );
};
