import { type ElementWrapperProps } from '@/screens/Chamber/components/ElementWrapper';
import { Spinner } from '@/components/Spinner';
import { Container } from './styles';
import PostHeader from '../PostHeader';
import PostBody from '../PostBody';
import { usePostElement } from '../../PostElementProvider';

const PostViewMode = (props: ElementWrapperProps) => {
  const { isCollapse, setIsCollapse, loading, noumEditor2 } = usePostElement();
  return (
    <Container
      data-testid="view-userpost"
      id="post"
      noumEditor2={noumEditor2 && !props.isCustomPreview}
    >
      <PostHeader
        {...props}
        isCollapse={isCollapse}
        setIsCollapse={setIsCollapse}
      />
      {loading && <Spinner />}
      <PostBody
        {...props}
        isCollapse={isCollapse}
        setIsCollapse={setIsCollapse}
      />
    </Container>
  );
};

export default PostViewMode;
