import { useMemo } from 'react';

import { CalendarGuestEventsListCollapsed } from '@/screens/Chamber/components/elements/CalendarEvent/components/CalendarGuestEventsListCollapsed';
import { CalendarEventsListExpanded } from '@/screens/Chamber/components/elements/CalendarEvent/components/CalendarEventsListExpanded';

import { type CalendarElementGuestViewProps } from './types';

export const CalendarElementGuestView = ({
  masterNoumId,
  events,
  eventMeta,
  isCollapsed,
  onEditEvent,
  onRefetchEvents,
  onGoLive,
  onJoin,
  onAccept,
  onDecline,
  onAttend,
  onAttending,
  onNotAttending,
  isNoumEditor2,
  isNoumLayoutSmallViewMode,
}: CalendarElementGuestViewProps) => {
  const eventHost: string = useMemo(
    () => events[0]?.chamberId?.uid?.firstName || '',
    [events],
  );

  if (isCollapsed) {
    return (
      <CalendarGuestEventsListCollapsed
        events={events}
        eventMeta={eventMeta}
        eventHost={eventHost}
        masterNoumId={masterNoumId}
        onGoLive={onGoLive}
        onAccept={onAccept}
        onDecline={onDecline}
        onAttending={onAttending}
        onNotAttending={onNotAttending}
        onEditEvent={onEditEvent}
        onJoinEvent={onJoin}
        onAttend={onAttend}
      />
    );
  }
  return (
    <CalendarEventsListExpanded
      masterNoumId={masterNoumId}
      events={events}
      eventHost={eventHost}
      onGoLive={onGoLive}
      onAccept={onAccept}
      onDecline={onDecline}
      onAttending={onAttending}
      onNotAttending={onNotAttending}
      onEditEvent={onEditEvent}
      onJoinEvent={onJoin}
      onAttend={onAttend}
      refetchEvents={onRefetchEvents}
      isNoumEditor2={isNoumEditor2}
      isNoumLayoutSmallViewMode={isNoumLayoutSmallViewMode}
    />
  );
};
