import { type FC, useCallback, useEffect, useMemo, useState } from 'react';
import { t } from 'i18next';
import {
  SpaceTypeEnum,
  ProjectChamberType,
  ConnectionRequestStatus,
  ConnectionRequestTypeEnum,
} from '@/apollo/generated/types';
import { Stack } from '@/layout';
import { TSpan } from '@/components/Typography';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { ConnectionDetailsModal } from '@/screens/Chamber/components/modals/ConnectionDetailsModal/ConnectionDetailsModal';
import { ConnectionInviteModal } from '@/screens/Chamber/components/modals/ConnectionInviteModal/ConnectionInviteModal';
import { NetworkStatus } from '@apollo/client';

import { useGetNoumByLinkContext } from '@/screens/Chamber/components/RightPanel/NoumByLinkProvider';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { useConnectionDetailsHelper } from '@/screens/Chamber/components/modals/ConnectionDetailsModal/useConnectionDetailsHelper';
import { ConnectionDetailModalTabEnum } from '@/screens/Chamber/components/modals/ConnectionDetailsModal/types';
import { useToggle } from '@/hooks';
import NoumDashboardMetricsModal from '@/screens/Chamber/components/modals/NoumDashboardMetricsModal';
import {
  NoumDashboardMetricsModalTabEnum,
  NoumDashboradTypeEnum,
} from '@/screens/Chamber/components/modals/NoumDashboardMetricsModal/types';
import { ChamberVisibilityInviteModal } from '@/screens/Chamber/components/modals/ChamberVisibilityInvite';
import {
  useRequestedConnections,
  useReceivedConnections,
} from '@/features/noums/hooks/spaceQuery';
import TokenModal from '@/screens/Money/Payments/Tokens/TokenModal';
import { useGetTokenQuery } from '@/apollo/graphql';
import { StyledSkeleton } from '@/components/ChamberBox/SkeletonChamberBox';
import { useEvents } from '@/features/events/hooks';
import { RequestsAndInvitesModal } from '../modals/RequestsAndInvites';
import { InfoStackWrapper, NoumsMemberButton } from './styles';
import { RISE_CATEGORY_NAME } from './types';

export const NoumMembersInformation: FC = () => {
  const {
    connectedMembersCount,
    followersCount: spaceFollowersCount = 0,
    isMasterNoum,
    space,
    isOwner,
    isFollowing,
    isConnected,
    refetchConnectedMembers,
    refetchFollowersCount,
    noumType,
    projectType,
    connectionStatus,
    lastUpdatedConnectionId,
    loadingSpace,
  } = useNoumContext();

  const { eventMeta, networkStatus } = useEvents({
    chamberId: space?._id || '',
    preventGetEvents: false,
  });
  const {
    data: tokenData,
    refetch: tokenRefetch,
    loading: loadingTokenData,
  } = useGetTokenQuery();
  const [defaultTab, setDefaultTab] = useState<string | undefined>();
  const [dashboardType, setDashboardType] = useState<
    NoumDashboradTypeEnum | undefined
  >();
  const [invitedNoumId, setInvitedNoumId] = useState<string | undefined>();
  const [isOpenInviteModal, setOpenInviteModal] = useState<boolean>(false);
  const [isQuickOpenInviteModal, setQuickOpenInviteModal] = useState(false);
  const [openTokenModal, setOpenTokenModal] = useState(false);
  const [isModalOpen, toggleModalOpen] = useToggle();
  const {
    flags: { noumDashboardMetrics },
  } = useLaunchDarkly();

  const isClickableConnections =
    isOwner || isConnected || (isMasterNoum && connectedMembersCount > 0);

  const isInviteBox = useMemo(
    () =>
      (projectType === ProjectChamberType.Public ||
        projectType === ProjectChamberType.Secret) &&
      !isMasterNoum,
    [isMasterNoum, projectType],
  );

  const {
    data: requestedConnection,
    refetch: refetchRequestConnection,
    loading: loadingRequestConnection,
  } = useRequestedConnections({
    limit: 3,
    requestFrom: isMasterNoum ? null : space?._id,
    status: ConnectionRequestStatus.Invited,
  });

  const {
    data: receivedConnections,
    refetch: refetchReceivedConnection,
    loading: loadingReceivedConnection,
  } = useReceivedConnections(
    isMasterNoum ? { limit: 3 } : { spaceId: space?._id, limit: 3 },
    isOwner,
  );

  const handleClickConnections = useCallback(() => {
    if (isClickableConnections) {
      if (isMasterNoum || !isOwner || !noumDashboardMetrics)
        setDefaultTab(ConnectionDetailModalTabEnum.Connections);
      else if (isOwner) {
        setDashboardType(NoumDashboradTypeEnum.connections);
        setDefaultTab(NoumDashboardMetricsModalTabEnum.Statistics);
      } else setDefaultTab(ConnectionDetailModalTabEnum.Connections);
    }
  }, [isClickableConnections, isMasterNoum, isOwner, noumDashboardMetrics]);

  const { noumLinkData, getLinkData, loadingLinked } =
    useGetNoumByLinkContext();

  const { totalCount, loading: connectionDetailsLoading } =
    useConnectionDetailsHelper(
      '',
      ConnectionDetailModalTabEnum.ProjectSpaces,
      undefined,
      isMasterNoum ? space?.uid?._id : null,
    );

  const connectedProjectsCount = isMasterNoum ? totalCount : 0;

  const { totalCount: userNoumsCount, loading: loadingUserNoums } =
    useConnectionDetailsHelper(
      '',
      ConnectionDetailModalTabEnum.OwnedNoums,
      undefined,
      isMasterNoum ? space?.uid?._id : null,
    );

  useEffect(() => {
    getLinkData(space?._id || '');
    refetchConnectedMembers();
    if (refetchFollowersCount && space?._id) refetchFollowersCount();
  }, [getLinkData, refetchConnectedMembers, refetchFollowersCount, space?._id]);

  const linkedNoums = useMemo(
    () => noumLinkData?.getNoumLinkByNoumId?.link?.linkedNoums || [],
    [noumLinkData?.getNoumLinkByNoumId?.link?.linkedNoums],
  );

  const linkedNoumsCount = useMemo(
    () => noumLinkData?.getNoumLinkByNoumId?.link?.linkedNoumsCount || 0,
    [noumLinkData?.getNoumLinkByNoumId?.link?.linkedNoumsCount],
  );

  const isLinked = useMemo(
    () => noumLinkData?.getNoumLinkByNoumId?.link || false,
    [noumLinkData],
  );

  const isSummaryDescrisption = useMemo(
    () => space?.description || space?.uid?.bio || undefined,
    [space?.description, space?.uid?.bio],
  );

  const followersCount = useMemo(
    () => (isLinked ? isLinked?.followersCount : spaceFollowersCount),
    [isLinked, spaceFollowersCount],
  );

  const showFollowers = useMemo(
    () =>
      space?.type !== SpaceTypeEnum.Home &&
      space?.projectType !== ProjectChamberType.Secret &&
      noumType !== SpaceTypeEnum.RiseApplication &&
      space?.category?.name !== RISE_CATEGORY_NAME,
    [noumType, space?.category?.name, space?.projectType, space?.type],
  );

  const showCompleteness = useMemo(
    () => !space?.percentCompleted || space?.percentCompleted < 100,
    [space?.percentCompleted],
  );

  const requestConnectionCount = useMemo(
    () =>
      requestedConnection?.requestedConnection?.count &&
      requestedConnection?.requestedConnection?.count > 0,
    [requestedConnection?.requestedConnection?.count],
  );

  const receivedConnectionCount = useMemo(
    () =>
      receivedConnections?.receivedConnectionRequest?.count &&
      receivedConnections?.receivedConnectionRequest?.count > 0,
    [receivedConnections?.receivedConnectionRequest?.count],
  );

  const info = useMemo(() => {
    if (tokenData?.getSpaceByType && tokenData?.getSpaceByType[0]) {
      return tokenData?.getSpaceByType[0];
    }
    return {
      token: {
        count: '--',
      },
    };
  }, [tokenData]);

  const refetchReceivedRequests = () =>
    refetchReceivedConnection(isMasterNoum ? {} : { spaceId: space?._id });

  useEffect(() => {
    if (
      lastUpdatedConnectionId &&
      requestedConnection?.requestedConnection?.data &&
      requestedConnection?.requestedConnection?.data?.findIndex(
        (x) => x?.connectionId === lastUpdatedConnectionId,
      ) > -1
    )
      refetchRequestConnection();
  }, [
    requestedConnection?.requestedConnection?.data,
    lastUpdatedConnectionId,
    refetchRequestConnection,
  ]);

  const rewardInfoCount = useMemo(
    () => typeof info.token?.count === 'number' && info.token.count > 0,
    [info.token?.count],
  );

  useEffect(() => {
    if (!showCompleteness) {
      tokenRefetch();
    }
  }, [showCompleteness, tokenRefetch]);

  useEffect(() => {
    getLinkData(space?._id || '');
    /* this needs to be called only when spaceFollowersCount is changed */
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [spaceFollowersCount]);

  return (
    <>
      {(isOwner ||
        isMasterNoum ||
        connectionStatus === ConnectionRequestTypeEnum.Approved) && (
        <InfoStackWrapper isSummaryDescription={!!isSummaryDescrisption}>
          {!loadingSpace &&
          connectedMembersCount > 0 &&
          noumType !== SpaceTypeEnum.RiseApplication ? (
            <NoumsMemberButton
              textOnly
              onClick={handleClickConnections}
              enableClick={isClickableConnections}
            >
              <Stack gap={4} align="center">
                <TSpan
                  font="body-m-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {connectedMembersCount.toLocaleString()}
                </TSpan>
                <TSpan
                  font="footnote-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {t('noumena.chamber.connections', {
                    postProcess: 'interval',
                    count: connectedMembersCount,
                  })}
                </TSpan>
              </Stack>
            </NoumsMemberButton>
          ) : (
            loadingSpace && <StyledSkeleton w={100} h={15} r={8} />
          )}
          {!loadingSpace && showFollowers && followersCount > 0 ? (
            <NoumsMemberButton
              textOnly
              onClick={() =>
                isOwner && setDefaultTab(ConnectionDetailModalTabEnum.Followers)
              }
              enableClick={isClickableConnections}
            >
              <Stack gap={4} align="center">
                <TSpan
                  font="body-m-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {followersCount.toLocaleString()}
                </TSpan>
                <TSpan
                  font="footnote-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {t('noumena.chamber.followers', {
                    postProcess: 'interval',
                    count: followersCount,
                  })}
                </TSpan>
              </Stack>
            </NoumsMemberButton>
          ) : (
            loadingSpace && <StyledSkeleton w={100} h={15} r={8} />
          )}

          {!loadingLinked && linkedNoums.length > 0 ? (
            <NoumsMemberButton
              textOnly
              onClick={() => setDefaultTab(ConnectionDetailModalTabEnum.Noums)}
              enableClick={isClickableConnections}
            >
              <Stack gap={4} align="center">
                <TSpan
                  font="body-m-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {linkedNoumsCount.toLocaleString()}
                </TSpan>
                <TSpan
                  font="footnote-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {t('noumena.noum_editor.linked_noum')}
                </TSpan>
              </Stack>
            </NoumsMemberButton>
          ) : (
            loadingLinked && <StyledSkeleton w={100} h={15} r={8} />
          )}
          {!loadingUserNoums && userNoumsCount > 0 ? (
            <NoumsMemberButton
              textOnly
              onClick={() =>
                setDefaultTab(ConnectionDetailModalTabEnum.OwnedNoums)
              }
              enableClick={isClickableConnections}
            >
              <Stack gap={4} align="center">
                <TSpan
                  font="body-m-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {userNoumsCount.toLocaleString()}
                </TSpan>
                <TSpan
                  font="footnote-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {t('noumena.noum_editor.owned_noum')}
                </TSpan>
              </Stack>
            </NoumsMemberButton>
          ) : (
            loadingUserNoums && <StyledSkeleton w={100} h={15} r={8} />
          )}

          {isOwner && isInviteBox ? (
            <>
              {!loadingRequestConnection && !!requestConnectionCount ? (
                <NoumsMemberButton
                  textOnly
                  onClick={toggleModalOpen}
                  enableClick={isClickableConnections}
                >
                  <Stack gap={4} align="center">
                    <TSpan
                      font="body-m-bold"
                      colorToken="--text-tablecell-header-neutral-default"
                    >
                      {(
                        requestedConnection?.requestedConnection?.count || 0
                      ).toLocaleString()}
                    </TSpan>
                    <TSpan
                      font="footnote-bold"
                      colorToken="--text-tablecell-header-neutral-default"
                    >
                      {t('noumena.chamber.modal.invited_by_me')}
                    </TSpan>
                  </Stack>
                </NoumsMemberButton>
              ) : (
                loadingRequestConnection && (
                  <StyledSkeleton w={100} h={15} r={8} />
                )
              )}
            </>
          ) : (!loadingRequestConnection || !loadingReceivedConnection) &&
            (!!receivedConnectionCount || !!requestConnectionCount) ? (
            <NoumsMemberButton
              textOnly
              onClick={toggleModalOpen}
              enableClick={isClickableConnections}
            >
              <Stack gap={4} align="center">
                <TSpan
                  font="body-m-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {(
                    (receivedConnections?.receivedConnectionRequest?.count ||
                      0) +
                    (requestedConnection?.requestedConnection?.count || 0)
                  ).toLocaleString()}
                </TSpan>
                <TSpan
                  font="footnote-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {t('noumena.chamber.modal.requests_invites')}
                </TSpan>
              </Stack>
            </NoumsMemberButton>
          ) : (
            (loadingRequestConnection || loadingReceivedConnection) && (
              <StyledSkeleton w={100} h={15} r={8} />
            )
          )}
          {networkStatus === NetworkStatus.ready &&
          (eventMeta?.hostedEventsCount ?? 0) > 0 ? (
            <NoumsMemberButton textOnly enableClick>
              <Stack gap={4} align="center">
                <TSpan
                  font="body-m-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {(eventMeta?.hostedEventsCount || 0).toLocaleString()}
                </TSpan>
                <TSpan
                  font="footnote-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {t('noumena.noum.events', {
                    postProcess: 'interval',
                    count: eventMeta?.hostedEventsCount || 0,
                  })}
                </TSpan>
              </Stack>
            </NoumsMemberButton>
          ) : (
            networkStatus !== NetworkStatus.ready && (
              <StyledSkeleton w={100} h={15} r={8} />
            )
          )}
          {!loadingTokenData && isOwner && isMasterNoum && !!rewardInfoCount ? (
            <NoumsMemberButton
              textOnly
              enableClick
              onClick={() => setOpenTokenModal(true)}
            >
              <Stack gap={4} align="center">
                <TSpan
                  font="body-m-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {info.token?.count.toLocaleString()}
                </TSpan>
                <TSpan
                  font="footnote-bold"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  {t('noumena.money.tokens.subheading')}
                </TSpan>
              </Stack>
            </NoumsMemberButton>
          ) : (
            loadingTokenData && <StyledSkeleton w={100} h={15} r={8} />
          )}
        </InfoStackWrapper>
      )}
      {(isFollowing ||
        isClickableConnections ||
        linkedNoums.length > 0 ||
        (isMasterNoum &&
          defaultTab === ConnectionDetailModalTabEnum.OwnedNoums)) &&
        !connectionDetailsLoading &&
        !loadingUserNoums && (
          <ConnectionDetailsModal
            isOpen={
              !!defaultTab &&
              defaultTab !== NoumDashboardMetricsModalTabEnum.Statistics
            }
            defaultTab={defaultTab}
            handleClose={() => setDefaultTab(undefined)}
            followersCount={followersCount}
            linkedNoumsCount={linkedNoumsCount}
            connectedProjectsCount={connectedProjectsCount}
            setInvitedNoumId={setInvitedNoumId}
            showInviteModal={() => setOpenInviteModal(true)}
            userNoumsCount={userNoumsCount}
          />
        )}
      <NoumDashboardMetricsModal
        isOpen={!!dashboardType}
        dashboardType={dashboardType}
        handleClose={() => setDashboardType(undefined)}
      />
      <ConnectionInviteModal
        isOpen={isOpenInviteModal}
        invitedNoumId={invitedNoumId}
        ownNoumId={space?._id || ''}
        closeInviteModal={() => setOpenInviteModal(false)}
      />
      <ChamberVisibilityInviteModal
        spaceId={space?._id || ''}
        isOpen={isQuickOpenInviteModal}
        handleClose={() => setQuickOpenInviteModal(false)}
        isOnlyInvite={true}
      />
      <RequestsAndInvitesModal
        isOpen={isModalOpen}
        isChambersScreen={isMasterNoum}
        handleClose={toggleModalOpen}
        noumId={space?._id || ''}
        isInviteOnly={isInviteBox}
        refetchReceivedRequests={refetchReceivedRequests}
      />
      <TokenModal
        open={openTokenModal}
        onClose={() => setOpenTokenModal(false)}
      />
    </>
  );
};
