import { useMemo } from 'react';

import { CalendarNoEvents } from '@/screens/Chamber/components/elements/CalendarEvent/components/CalendarNoEvents';
import { CalendarOwnerEventsListCollapsed } from '@/screens/Chamber/components/elements/CalendarEvent/components/CalendarOwnerEventsListCollapsed';
import { CalendarEventsListExpanded } from '@/screens/Chamber/components/elements/CalendarEvent/components/CalendarEventsListExpanded';

import { Spinner } from '@/components/Spinner';
import { EventFilterDropDown } from '@/features/events/components';
import { CalendarElementViewModeSubHeader } from './CalendarElementViewModeSubHeader';

import { type CalendarElementOwnerViewProps } from './types';
import { LoadingContainer } from '../components/styles';

export const CalendarElementOwnerView = ({
  masterNoumId,
  loading,
  events,
  eventMeta,
  eventsCount,
  isCollapsed,
  activeFilter,
  activeViewMode,
  onChangeFilter,
  onFilterChanged,
  onChangeViewMode,
  onAddEvent,
  onEditEvent,
  onRefetchEvents,
  onGoLive,
  onJoin,
  onAccept,
  onDecline,
  onAttend,
  onAttending,
  onNotAttending,
  onCopyLink,
  isNoumEditor2,
  isNoumLayoutSmallViewMode,
}: CalendarElementOwnerViewProps) => {
  const showFilters = useMemo(() => {
    if (isCollapsed) return false;

    return eventsCount ? true : activeFilter !== 'all';
  }, [activeFilter, eventsCount, isCollapsed]);

  const showOwnerCollapsedTabs = useMemo(() => {
    if (!isCollapsed) return false;

    return Boolean(eventMeta?.allEventsCount);
  }, [eventMeta?.allEventsCount, isCollapsed]);

  const showNoEvents = useMemo(() => {
    if (eventsCount) return false;
    if (isCollapsed) {
      return !eventMeta?.allEventsCount;
    }

    return true;
  }, [eventMeta?.allEventsCount, eventsCount, isCollapsed]);

  return (
    <>
      {!isNoumLayoutSmallViewMode && showFilters && (
        <CalendarElementViewModeSubHeader
          activeFilter={activeFilter}
          activeViewMode={activeViewMode}
          onFilterChange={onChangeFilter}
          onViewModeChange={onChangeViewMode}
          isNoumEditor2={isNoumEditor2}
        />
      )}
      {isNoumLayoutSmallViewMode && Boolean(eventMeta?.allEventsCount) && (
        <EventFilterDropDown
          activeFilter={activeFilter}
          onChange={onChangeFilter}
        />
      )}
      {loading ? (
        <LoadingContainer>
          <Spinner />
        </LoadingContainer>
      ) : (
        <>
          {showOwnerCollapsedTabs && (
            <CalendarOwnerEventsListCollapsed
              activeFilter={activeFilter}
              onFilterChange={onFilterChanged}
              eventMeta={eventMeta}
            />
          )}
          {eventsCount > 0 && !isCollapsed && (
            <CalendarEventsListExpanded
              eventsCount={eventsCount}
              masterNoumId={masterNoumId}
              events={events}
              eventHost=""
              onGoLive={onGoLive}
              onAccept={onAccept}
              onDecline={onDecline}
              onAttending={onAttending}
              onNotAttending={onNotAttending}
              onEditEvent={onEditEvent}
              onJoinEvent={onJoin}
              onAttend={onAttend}
              refetchEvents={onRefetchEvents}
              onCopyLink={onCopyLink}
              isNoumEditor2={isNoumEditor2}
              isNoumLayoutSmallViewMode={isNoumLayoutSmallViewMode}
            />
          )}
          {showNoEvents && (
            <CalendarNoEvents
              isCollapsed={isCollapsed}
              activeFilter={activeFilter}
              loading={loading}
              onAddEvent={onAddEvent}
              isNoumEditor2={isNoumEditor2}
            />
          )}
        </>
      )}
    </>
  );
};
