import {
  type MouseEvent,
  useEffect,
  useMemo,
  useState,
  useRef,
  Fragment,
} from 'react';
import { useTranslation } from 'react-i18next';
import { Icon } from '@/components/Icon';
import { Spinner } from '@/components/Spinner';
import { isValidFileSize } from '@/utils/file';
import { useClickOutside, useToast } from '@/hooks';
import { TSpan } from '@/components/Typography';
import { Dropdown, type DropdownTargetProps } from '@/components/Dropdown';
import { Stack } from '@/layout';
import { bannerEditOptions, PROFILE_RADIUS, PROFLE_SIZE } from './constants';
import {
  BannerDescription,
  DropDownBannerbutton,
  DropDownItems,
  ProfileBannerImage,
  StyledAvatarContainer,
} from './styles';
import { type AvatarProps, ProfileSize } from './types';
import { BannerEditButton } from './BannerEditButton';

export const ProfileBanner = ({
  disabled,
  url,
  size,
  fileSize,
  maximumFileSize,
  editable,
  isUploadComplete,
  isUploadStarted,
  onClick,
  onClose,
  width: customWidth,
  height: customHeight,
  borderRadius: customBorderRadius,
  opacity,
  isBanner = true,
  onClear,
  loading,
}: AvatarProps) => {
  const [isFocused, setisFocused] = useState(false);
  const height = customWidth || PROFLE_SIZE[size || ProfileSize.L];
  const containerRef = useRef(null);
  useClickOutside(containerRef, true, () => setisFocused(false));
  const borderRadius =
    customBorderRadius ?? PROFILE_RADIUS[size || ProfileSize.L];

  const { t } = useTranslation();
  const { addToast } = useToast();
  const isUploading = useMemo(
    () => (isUploadStarted && !isUploadComplete) || false,
    [isUploadComplete, isUploadStarted],
  );

  const isCoverUrl = useMemo(
    () => (isBanner && editable && !url) || false,
    [editable, isBanner, url],
  );

  const handleClick = (event: MouseEvent<HTMLInputElement>) => {
    event.preventDefault();
    if (editable) onClick?.(event);
  };

  useEffect(() => {
    if (editable && !isValidFileSize(fileSize || 0, maximumFileSize)) {
      onClose?.();
    }
  }, [editable, fileSize, maximumFileSize, addToast, t, onClose]);

  const handleSelectDropdown = (
    event: MouseEvent<HTMLInputElement>,
    selectedDropdown?: string,
  ) => {
    setisFocused(false);
    switch (selectedDropdown) {
      case 'Add New Photo':
        handleClick(event);
        break;
      case 'Remove':
        onClear?.();
        break;
    }
  };

  return (
    <>
      <StyledAvatarContainer
        aria-label="profile-banner"
        data-testid="avatarContainer"
        size={isBanner ? customHeight! : height}
        radius={borderRadius}
        disabled={disabled}
        opacity={opacity}
        isBanner={isBanner}
        isCoverUrl={isCoverUrl}
      >
        {url ? (
          <ProfileBannerImage
            src={url}
            size={isBanner ? customHeight : customWidth || height}
            isBanner={isBanner}
          />
        ) : !isBanner ? (
          <Icon
            imageIconName="avatar_m"
            size={height}
            data-testid="avatarIcon"
          />
        ) : (
          editable && (
            <Icon
              name="picture_m"
              size={150}
              data-testid="avatarIcon"
              color="--color-base-gray-90"
            />
          )
        )}
        {(isUploading || loading) && <Spinner />}
        {!isUploading && editable && (
          <div ref={containerRef}>
            {isBanner && url ? (
              <Dropdown
                containerWidth="max-content"
                options={bannerEditOptions}
                isAnimation={false}
                usePortal={false}
                observerMinHeight="0"
                renderContainerFromBottom
                isOpen={isFocused}
                optionsRenderer={() => (
                  <Stack vertical>
                    {bannerEditOptions.map((item) => (
                      <DropDownItems
                        fullWidth
                        padding="13px"
                        gap={21}
                        borderBottom
                        align="center"
                        onClick={(e) => {
                          e.stopPropagation();
                          handleSelectDropdown(
                            e as MouseEvent<HTMLInputElement>,
                            item.value,
                          );
                        }}
                      >
                        {item.icon}
                        <TSpan
                          font="body-m-bold"
                          colorToken={
                            item.intent === 'danger'
                              ? '--text-tablecell-header-danger-primary-highlighted'
                              : undefined
                          }
                        >
                          {item.label}
                        </TSpan>
                      </DropDownItems>
                    ))}
                  </Stack>
                )}
              >
                {({
                  targetRef,
                  targetProps,
                }: DropdownTargetProps<HTMLButtonElement>) => (
                  <DropDownBannerbutton
                    ref={targetRef}
                    {...targetProps}
                    size="small"
                    isFocused={isFocused}
                    leftIcon={
                      <Icon
                        size={24}
                        name="picture_m"
                        color="--icon-tag-neutral-alt-default"
                      />
                    }
                    color="--icon-tag-neutral-alt-default"
                    onClick={() => setisFocused(!isFocused)}
                  >
                    <TSpan
                      font="button-s"
                      colorToken="--bg-card-neutral-alt-default"
                    >
                      {t(`noumena.avatar.editable_button_text`)}
                    </TSpan>
                  </DropDownBannerbutton>
                )}
              </Dropdown>
            ) : (
              <BannerEditButton
                size={size}
                name="picture_m"
                color="--icon-tag-neutral-alt-default"
                onClick={handleClick}
                isBanner={isBanner}
              />
            )}
          </div>
        )}
        {isBanner && editable && !url && (
          <BannerDescription>
            <TSpan font="button-s" colorToken="--text-button-neutral-disabled">
              {t(`noumena.editor.Cover_photo_description`)}
            </TSpan>
          </BannerDescription>
        )}
      </StyledAvatarContainer>
    </>
  );
};
