import { useEffect, useState } from 'react';
import { t } from 'i18next';
import { useGetQuestionsForSpaceQuery } from '@/apollo/graphql';
import { useToast } from '@/hooks/toast';
import {
  type QuestionStatusEnum,
  SortOperator,
} from '@/apollo/generated/types';
import { PAGE_SIZE } from '../constants';

export function useGetQuickQuestionHelper(
  spaceId: string,
  status: QuestionStatusEnum | undefined,
  isCollapse: boolean,
  offset: number,
) {
  const { addToast } = useToast();

  const [counts, setCounts] = useState({
    openQuestionCount: 0,
    closeQuestionCount: 0,
    answeredQuestionCount: 0,
    tippedQuestionCount: 0,
    totalQuestionCount: 0,
  });

  const variables = {
    spaceId,
    sort: {
      column: 'createdAt',
      operator: SortOperator.Desc,
    },
    limit: isCollapse ? 1 : PAGE_SIZE,
    filter: isCollapse ? {} : { status },
    offset,
  };

  const { data, loading, error, refetch } = useGetQuestionsForSpaceQuery({
    variables,
    skip: (!isCollapse && !status) || !spaceId,
    fetchPolicy: 'cache-and-network',
  });

  useEffect(() => {
    if (!data) return;

    const openQuestionCount = data?.open?.count || 0;
    const closeQuestionCount = data?.close?.count || 0;
    const answeredQuestionCount = data?.answered?.count || 0;
    const tippedQuestionCount = data?.tipped?.count || 0;
    const totalQuestionCount = openQuestionCount + closeQuestionCount;

    setCounts({
      openQuestionCount,
      closeQuestionCount,
      answeredQuestionCount,
      tippedQuestionCount,
      totalQuestionCount,
    });
  }, [data]);

  useEffect(() => {
    if (error) {
      let message = t('noumena.error.unknown');
      if (error instanceof Error) {
        message = error.message;
      }
      addToast('error', 'none', message);
    }
  }, [addToast, error]);

  return {
    data,
    loading,
    refetch,
    ...counts,
  };
}
