import { useCallback } from 'react';
import * as Sentry from '@sentry/react';
import {
  GetSpaceByIdDocument,
  type GetSpaceByIdQuery,
  type GetSpaceByIdQueryVariables,
  useCreateNoumLayoutSectionMutation,
} from '@/apollo/graphql';
import { cloneDeep } from 'lodash';
import {
  type NoumLayoutSection,
  NoumLayoutStatusFilter,
  type CreateNoumLayoutSectionInput,
  NoumLayoutStatus,
} from '@/apollo/generated/types';
import { useToast } from '@/hooks/toast';

export function useAddSectionsHelper() {
  const { addToast } = useToast();

  const [addSections, { loading }] = useCreateNoumLayoutSectionMutation();

  const addSectionsHelper = useCallback(
    async (input: CreateNoumLayoutSectionInput, spaceId: string) => {
      let isSuccess;
      try {
        await addSections({
          variables: { input },
          update: (cache, { data }) => {
            if (!data || !data.createNoumLayoutSection) return;

            const variables: GetSpaceByIdQueryVariables = {
              id: spaceId,
              editorV2Enabled: true,
              status: NoumLayoutStatusFilter.Unpublished,
            };

            const cacheResult = cache.readQuery<GetSpaceByIdQuery>({
              query: GetSpaceByIdDocument,
              variables,
            });

            if (!cacheResult) return;

            const { getSpaceById: spaceData } = cacheResult;

            if (!spaceData) return;

            const cloneSpaceData = cloneDeep(spaceData);
            if (!cloneSpaceData?.layout) return;
            cloneSpaceData.layout.status = NoumLayoutStatus.Unsaved;
            cloneSpaceData.layout.sections.reduce(
              (acc: NoumLayoutSection[], obj) => {
                if (obj.position >= data.createNoumLayoutSection.position) {
                  const cloneObj = obj;
                  cloneObj.position += 1;
                }
                acc.push(obj);
                return acc;
              },
              [],
            );
            cloneSpaceData.layout.sections.splice(
              data.createNoumLayoutSection.position - 1,
              0,
              data.createNoumLayoutSection,
            );
            cache.writeQuery({
              query: GetSpaceByIdDocument,
              variables,
              data: {
                getSpaceById: cloneSpaceData,
              },
            });
          },
        });

        isSuccess = true;
      } catch (error) {
        let message = 'Unknown';
        if (error instanceof Error) {
          message = error.message;
        }
        addToast('error', 'none', message);
        Sentry.captureException(new Error(message), {
          tags: {
            section: 'addElementMutation',
          },
        });

        isSuccess = false;
      }

      return isSuccess;
    },
    [addSections, addToast],
  );

  return {
    loading,
    addSectionsHelper,
  };
}
